import { useState, useEffect, useCallback, useRef } from 'react';

// Orlando coordinates as fallback
const DEFAULT_LOCATION = {
    latitude: 28.5383,
    longitude: -81.3792,
    name: 'Orlando, FL (Default)'  // Make it obvious this is a default
};

/**
 * Hook for getting the user's geolocation
 */
const useGeolocation = (options = {}) => {
    const [location, setLocation] = useState(null);
    const [error, setError] = useState(null);
    const [loading, setLoading] = useState(false);
    const [permissionState, setPermissionState] = useState('prompt');

    // Prevent multiple simultaneous requests
    const requestInProgressRef = useRef(false);

    // Track if location has been requested by user
    const [userRequested, setUserRequested] = useState(false);

    // Handle successful location
    const handleSuccess = useCallback((position) => {
        setLoading(false);
        setError(null);
        requestInProgressRef.current = false;

        setLocation({
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
            accuracy: position.coords.accuracy,
            timestamp: position.timestamp
        });

        setPermissionState('granted');
    }, []);

    // Handle location error
    const handleError = useCallback((error) => {
        setLoading(false);
        requestInProgressRef.current = false;
        setError(error.message);

        if (error.code === error.PERMISSION_DENIED) {
            setPermissionState('denied');
        }

        console.log('Geolocation error:', error, 'Using default location');

        // Use default location
        setLocation(DEFAULT_LOCATION);
    }, []);

    // Function to request current position
    const getCurrentPosition = useCallback(() => {
        // Prevent multiple simultaneous requests
        if (requestInProgressRef.current) {
            console.log('Location request already in progress');
            return;
        }

        setUserRequested(true);

        if (!navigator.geolocation) {
            setError('Geolocation is not supported by your browser');
            setLocation(DEFAULT_LOCATION);
            return;
        }

        setLoading(true);
        requestInProgressRef.current = true;

        navigator.geolocation.getCurrentPosition(
            handleSuccess,
            handleError,
            options
        );
    }, [handleError, handleSuccess, options]);

    // Check permission state on mount - only once
    useEffect(() => {
        if (navigator.permissions) {
            navigator.permissions.query({ name: 'geolocation' }).then(result => {
                setPermissionState(result.state);

                // Set up permission change listener
                result.addEventListener('change', () => {
                    setPermissionState(result.state);
                });
            });
        }
    }, []);

    // Try to get location on initial render if allowed
    useEffect(() => {
        // Don't automatically request if previously denied
        if (permissionState === 'granted' && !location && !userRequested) {
            getCurrentPosition();
        } else if (permissionState === 'denied' && !location) {
            // Use default if permission denied
            setLocation(DEFAULT_LOCATION);
        }
    }, [permissionState, location, getCurrentPosition, userRequested]);

    return {
        location,
        error,
        loading,
        getCurrentPosition,
        permissionState,
        DEFAULT_LOCATION
    };
};

export default useGeolocation;