import React, { useRef, useEffect, useState } from 'react';
import { Box, Typography, Card, CardContent, useMediaQuery } from '@mui/material';
import { useTheme } from '@mui/material/styles';

const CountdownClock = ({
    targetTime,
    title,
    subtitle,
    description
}) => {
    const theme = useTheme();
    const canvasRef = useRef(null);
    const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());
    const [scale, setScale] = useState(1);

    // Responsive breakpoints
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
    const isMedium = useMediaQuery(theme.breakpoints.down('md'));

    // Animation frame ref
    const animationFrameRef = useRef(null);

    // Calculate time left between now and target time
    function calculateTimeLeft() {
        const difference = targetTime - new Date();

        if (difference <= 0) {
            return {
                hours: 0,
                minutes: 0,
                seconds: 0,
                total: 0
            };
        }

        return {
            hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
            minutes: Math.floor((difference / 1000 / 60) % 60),
            seconds: Math.floor((difference / 1000) % 60),
            total: difference
        };
    }

    // Set up pulse animation and timer
    useEffect(() => {
        // Update countdown every second
        const timer = setInterval(() => {
            setTimeLeft(calculateTimeLeft());
        }, 1000);

        // Set up pulse animation
        let direction = 1;
        let currentScale = 1;

        const animatePulse = () => {
            if (currentScale >= 1.05) direction = -1;
            if (currentScale <= 0.95) direction = 1;

            currentScale += direction * 0.002;
            setScale(currentScale);

            animationFrameRef.current = requestAnimationFrame(animatePulse);
        };

        animationFrameRef.current = requestAnimationFrame(animatePulse);

        return () => {
            clearInterval(timer);
            if (animationFrameRef.current) {
                cancelAnimationFrame(animationFrameRef.current);
            }
        };
    }, [targetTime]);

    // Draw clock face and countdown
    useEffect(() => {
        const canvas = canvasRef.current;
        if (!canvas) return;

        // Determine appropriate size based on screen size
        const size = isMobile ? 240 : 300;

        const ctx = canvas.getContext('2d');

        // Set canvas size accounting for device pixel ratio
        const dpr = window.devicePixelRatio || 1;
        canvas.width = size * dpr;
        canvas.height = size * dpr;
        ctx.scale(dpr, dpr);
        canvas.style.width = `${size}px`;
        canvas.style.height = `${size}px`;

        // Clear canvas
        ctx.clearRect(0, 0, canvas.width, canvas.height);

        // Calculate progress for clock face
        const totalSeconds = 60 * 60; // 1 hour standard
        const remainingSeconds = (timeLeft.hours * 3600) + (timeLeft.minutes * 60) + timeLeft.seconds;
        const progress = Math.min(remainingSeconds / totalSeconds, 1);

        // Draw countdown clock
        drawCountdownClock(ctx, size, progress);

    }, [timeLeft, isMobile]);

    // Draw the countdown clock face
    const drawCountdownClock = (ctx, size, progress) => {
        const center = { x: size / 2, y: size / 2 };
        const radius = (size / 2) - 10;
        const strokeWidth = isMobile ? 6 : 8;

        // Draw background circle
        ctx.beginPath();
        ctx.arc(center.x, center.y, radius, 0, 2 * Math.PI);
        ctx.strokeStyle = `${theme.palette.divider}4D`; // 30% opacity
        ctx.lineWidth = strokeWidth;
        ctx.stroke();

        // Draw progress arc
        ctx.beginPath();
        ctx.arc(
            center.x, center.y, radius,
            -Math.PI / 2, // Start at the top
            -Math.PI / 2 + (2 * Math.PI * progress), // End based on progress
            false // Counter-clockwise = false
        );

        // Create gradient for progress arc
        const gradient = ctx.createLinearGradient(
            center.x - radius, center.y,
            center.x + radius, center.y
        );
        gradient.addColorStop(0, theme.palette.secondary.main);
        gradient.addColorStop(1, theme.palette.primary.main);

        ctx.strokeStyle = gradient;
        ctx.lineWidth = strokeWidth;
        ctx.lineCap = 'round';
        ctx.stroke();

        // Draw tick marks for hours
        for (let i = 0; i < 12; i++) {
            const angle = i * Math.PI / 6;
            const isMainMarker = i % 3 === 0;

            ctx.beginPath();
            ctx.strokeStyle = isMainMarker ? theme.palette.primary.main : `${theme.palette.primary.main}99`;
            ctx.lineWidth = isMainMarker ? 3 : 1;

            const markerLength = isMainMarker ? 15 : 8;
            const outerX = center.x + (radius + 5) * Math.cos(angle);
            const outerY = center.y + (radius + 5) * Math.sin(angle);
            const innerX = center.x + (radius + 5 + markerLength) * Math.cos(angle);
            const innerY = center.y + (radius + 5 + markerLength) * Math.sin(angle);

            ctx.moveTo(outerX, outerY);
            ctx.lineTo(innerX, innerY);
            ctx.stroke();
        }
    };

    // Helper function to pad digits with leading zeros
    const padDigit = (num) => {
        return num.toString().padStart(2, '0');
    };

    // Determine size based on screen size
    const clockSize = isMobile ? 240 : 300;
    const fontSize = {
        // Responsive font sizes
        xs: isMobile ? 38 : (isMedium ? 42 : 58),
        sm: isMobile ? 16 : 20,
        md: isMobile ? 22 : 28
    };

    return (
        <Card>
            <CardContent sx={{ textAlign: 'center', p: isMobile ? 2 : 3 }}>
                {title && (
                    <Typography variant="h5" gutterBottom color="primary">
                        {title}
                    </Typography>
                )}

                {subtitle && (
                    <Typography variant="subtitle1" gutterBottom color="text.secondary">
                        {subtitle}
                    </Typography>
                )}

                <Box
                    sx={{
                        position: 'relative',
                        width: clockSize,
                        height: clockSize,
                        margin: '0 auto',
                        transform: `scale(${scale})`,
                        transition: 'transform 0.2s ease-in-out'
                    }}
                >
                    {/* Clock Canvas */}
                    <canvas
                        ref={canvasRef}
                        style={{ position: 'absolute', top: 0, left: 0 }}
                    />

                    {/* Digital Countdown */}
                    <Box
                        sx={{
                            position: 'absolute',
                            top: '50%',
                            left: '50%',
                            transform: 'translate(-50%, -50%)',
                            width: '100%',
                            textAlign: 'center'
                        }}
                    >
                        <Typography
                            sx={{
                                fontWeight: 700,
                                lineHeight: 1.1,
                                mb: 1,
                                fontSize: fontSize.xs,  // Smaller font size
                                fontFamily: 'monospace',  // Fixed-width font for better alignment
                                letterSpacing: '-0.05em'  // Tighter letter spacing
                            }}
                        >
                            {padDigit(timeLeft.hours)}:{padDigit(timeLeft.minutes)}:{padDigit(timeLeft.seconds)}
                        </Typography>

                        {description && (
                            <Typography
                                variant="subtitle1"
                                sx={{
                                    color: theme.palette.text.secondary,
                                    mt: 1,
                                    fontSize: fontSize.sm
                                }}
                            >
                                {description}
                            </Typography>
                        )}
                    </Box>
                </Box>
            </CardContent>
        </Card>
    );
};

export default CountdownClock;