import React, { useState, useEffect } from 'react';
import { Box, Grid, Card, CardContent, Typography, Divider, CircularProgress, Paper } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import { usePrayerTimes } from '../../contexts/PrayerTimesContext';
import PrayerClock from './PrayerClock';
import CountdownClock from './CountdownClock';
import { formatTime12Hour } from '../../utils/formatters';

// Enable this for debugging
const DEBUG = true;

const EnhancedPrayerTimesDisplay = () => {
    const theme = useTheme();
    const { prayerTimes, hijriDate, params, loading, fetchPrayerTimesByCoordinates } = usePrayerTimes();

    const [currentTime, setCurrentTime] = useState(new Date());
    const [nextPrayer, setNextPrayer] = useState(null);
    const [remainingTime, setRemainingTime] = useState(null);
    const [progress, setProgress] = useState(0);
    const [calculationState, setCalculationState] = useState('initial');

    // Debug logging
    useEffect(() => {
        if (DEBUG) {
            console.log("Prayer Times State:", {
                prayerTimes,
                loading,
                nextPrayer,
                calculationState
            });
        }
    }, [prayerTimes, loading, nextPrayer, calculationState]);

    // Update current time every second
    useEffect(() => {
        const timer = setInterval(() => {
            setCurrentTime(new Date());
        }, 1000);

        return () => clearInterval(timer);
    }, []);

    // Try to refresh data if it's not available
    useEffect(() => {
        if (!prayerTimes && !loading && typeof fetchPrayerTimesByCoordinates === 'function') {
            if (DEBUG) console.log("No prayer times available, attempting to fetch...");
            fetchPrayerTimesByCoordinates();
        }
    }, [prayerTimes, loading, fetchPrayerTimesByCoordinates]);

    // Calculate next prayer time and countdown
    useEffect(() => {
        if (!prayerTimes) {
            if (DEBUG) console.log("No prayer times data available");
            return;
        }

        try {
            setCalculationState('calculating');

            if (DEBUG) {
                console.log("Starting prayer time calculation with:", prayerTimes);
            }

            // Main prayer times
            const mainPrayers = [
                { name: 'Fajr', time: prayerTimes.Fajr },
                { name: 'Dhuhr', time: prayerTimes.Dhuhr },
                { name: 'Asr', time: prayerTimes.Asr },
                { name: 'Maghrib', time: prayerTimes.Maghrib },
                { name: 'Isha', time: prayerTimes.Isha }
            ];

            // Check for valid prayer times
            const validPrayers = mainPrayers.filter(p => p.time && typeof p.time === 'string');

            if (validPrayers.length === 0) {
                if (DEBUG) console.error("No valid prayer times found:", mainPrayers);
                setCalculationState('no-valid-prayers');
                return;
            }

            const now = currentTime;
            const currentHour = now.getHours();
            const currentMinute = now.getMinutes();
            const currentTotalMinutes = currentHour * 60 + currentMinute;

            if (DEBUG) {
                console.log("Current time:", {
                    currentHour,
                    currentMinute,
                    currentTotalMinutes
                });
            }

            // Find next prayer
            let nextPrayerInfo = null;
            let previousPrayerInfo = null;

            for (const prayer of mainPrayers) {
                // Skip if time is not available or invalid
                if (!prayer.time) {
                    if (DEBUG) console.log(`Skipping prayer ${prayer.name}, no time available`);
                    continue;
                }

                try {
                    const [prayerHour, prayerMinute] = prayer.time.split(':').map(Number);

                    if (isNaN(prayerHour) || isNaN(prayerMinute)) {
                        if (DEBUG) console.log(`Invalid time for ${prayer.name}: ${prayer.time}`);
                        continue;
                    }

                    const prayerTotalMinutes = prayerHour * 60 + prayerMinute;

                    if (DEBUG) {
                        console.log(`Processing ${prayer.name}`, {
                            time: prayer.time,
                            prayerHour,
                            prayerMinute,
                            prayerTotalMinutes,
                            isAfterCurrent: prayerTotalMinutes > currentTotalMinutes
                        });
                    }

                    if (prayerTotalMinutes > currentTotalMinutes) {
                        if (!nextPrayerInfo || prayerTotalMinutes < nextPrayerInfo.totalMinutes) {
                            nextPrayerInfo = {
                                ...prayer,
                                totalMinutes: prayerTotalMinutes,
                                dateTime: new Date(
                                    now.getFullYear(),
                                    now.getMonth(),
                                    now.getDate(),
                                    prayerHour,
                                    prayerMinute
                                )
                            };
                            if (DEBUG) console.log(`Found next prayer: ${prayer.name}`);
                        }
                    } else {
                        if (!previousPrayerInfo || prayerTotalMinutes > previousPrayerInfo.totalMinutes) {
                            previousPrayerInfo = {
                                ...prayer,
                                totalMinutes: prayerTotalMinutes
                            };
                            if (DEBUG) console.log(`Found previous prayer: ${prayer.name}`);
                        }
                    }
                } catch (err) {
                    console.error(`Error processing prayer ${prayer.name}:`, err);
                }
            }

            // After processing all prayers, ensure we have previous prayer info
            if (nextPrayerInfo && !previousPrayerInfo) {
                // If we have a next prayer but no previous prayer, create one
                if (DEBUG) console.log("Found next prayer but no previous prayer, creating synthetic previous...");

                // Find the prayer that would logically come before the next prayer
                const prayerOrder = ['Fajr', 'Dhuhr', 'Asr', 'Maghrib', 'Isha'];
                const nextPrayerBaseName = nextPrayerInfo.name.replace(' (Tomorrow)', '');
                const nextIndex = prayerOrder.indexOf(nextPrayerBaseName);

                // If next is first in order or not found, previous is the last one
                const prevIndex = nextIndex > 0 ? nextIndex - 1 : prayerOrder.length - 1;
                const prevPrayerName = prayerOrder[prevIndex];

                // Find this prayer in mainPrayers
                const prevPrayer = mainPrayers.find(p => p.name === prevPrayerName);

                if (prevPrayer && prevPrayer.time) {
                    const [prevHour, prevMinute] = prevPrayer.time.split(':').map(Number);
                    if (!isNaN(prevHour) && !isNaN(prevMinute)) {
                        previousPrayerInfo = {
                            ...prevPrayer,
                            totalMinutes: prevHour * 60 + prevMinute
                        };

                        if (DEBUG) console.log(`Created synthetic previous prayer: ${prevPrayerName}`);
                    }
                }
                // If we still don't have a previous prayer, try using the last prayer in order
                else if (!previousPrayerInfo) {
                    for (let i = prayerOrder.length - 1; i >= 0; i--) {
                        const alternatePrayer = mainPrayers.find(p => p.name === prayerOrder[i]);
                        if (alternatePrayer && alternatePrayer.time) {
                            const [altHour, altMinute] = alternatePrayer.time.split(':').map(Number);
                            if (!isNaN(altHour) && !isNaN(altMinute)) {
                                previousPrayerInfo = {
                                    ...alternatePrayer,
                                    totalMinutes: altHour * 60 + altMinute
                                };
                                if (DEBUG) console.log(`Created alternate previous prayer: ${alternatePrayer.name}`);
                                break;
                            }
                        }
                    }
                }
            }

            // If no next prayer found, it's Fajr tomorrow
            if (!nextPrayerInfo && mainPrayers[0].time) {
                try {
                    const fajr = mainPrayers[0];
                    const [fajrHour, fajrMinute] = fajr.time.split(':').map(Number);

                    if (!isNaN(fajrHour) && !isNaN(fajrMinute)) {
                        const tomorrow = new Date(now);
                        tomorrow.setDate(tomorrow.getDate() + 1);
                        tomorrow.setHours(fajrHour, fajrMinute, 0, 0);

                        nextPrayerInfo = {
                            ...fajr,
                            name: 'Fajr (Tomorrow)',
                            totalMinutes: fajrHour * 60 + fajrMinute,
                            dateTime: tomorrow
                        };

                        if (DEBUG) console.log("Set next prayer to tomorrow's Fajr");

                        if (!previousPrayerInfo && mainPrayers[4].time) {
                            const [ishaHour, ishaMinute] = mainPrayers[4].time.split(':').map(Number);
                            if (!isNaN(ishaHour) && !isNaN(ishaMinute)) {
                                previousPrayerInfo = {
                                    ...mainPrayers[4],
                                    totalMinutes: ishaHour * 60 + ishaMinute
                                };
                                if (DEBUG) console.log("Set previous prayer to today's Isha");
                            }
                        }
                    } else {
                        if (DEBUG) console.error("Invalid Fajr time format:", fajr.time);
                    }
                } catch (err) {
                    console.error("Error setting tomorrow's Fajr:", err);
                }
            }

            // If we have at least nextPrayerInfo, we can proceed with calculations
            if (nextPrayerInfo) {
                try {
                    // Set the next prayer regardless of calculation success
                    setNextPrayer(nextPrayerInfo);

                    if (nextPrayerInfo.dateTime) {
                        const msUntilNextPrayer = nextPrayerInfo.dateTime - now;
                        setRemainingTime(msUntilNextPrayer);
                    }

                    // If we have both prayers, calculate progress accurately
                    if (previousPrayerInfo) {
                        const totalInterval = (nextPrayerInfo.totalMinutes - previousPrayerInfo.totalMinutes +
                            (nextPrayerInfo.name && nextPrayerInfo.name.includes('Tomorrow') ? 24 * 60 : 0));

                        // Adjust for crossing midnight
                        let elapsed;
                        if (previousPrayerInfo.totalMinutes > nextPrayerInfo.totalMinutes &&
                            !nextPrayerInfo.name.includes('Tomorrow')) {
                            // Next prayer is tomorrow, but not marked as such
                            elapsed = currentTotalMinutes - previousPrayerInfo.totalMinutes + (24 * 60);
                        } else if (previousPrayerInfo.totalMinutes > currentTotalMinutes &&
                            nextPrayerInfo.name.includes('Tomorrow')) {
                            // Crossing midnight scenario
                            elapsed = currentTotalMinutes + (24 * 60) - previousPrayerInfo.totalMinutes;
                        } else {
                            // Normal scenario
                            elapsed = currentTotalMinutes - previousPrayerInfo.totalMinutes;
                        }

                        const calculatedProgress = Math.min(Math.max(elapsed / totalInterval, 0), 1);

                        if (DEBUG) {
                            console.log("Final calculation results:", {
                                msUntilNextPrayer: nextPrayerInfo.dateTime - now,
                                totalInterval,
                                elapsed,
                                calculatedProgress
                            });
                        }

                        setProgress(calculatedProgress);
                        setCalculationState('complete');
                    } else {
                        // Fallback progress - show half progress if previous prayer unknown
                        if (DEBUG) console.log("Missing previous prayer, using default progress");
                        setProgress(0.5);
                        setCalculationState('partial-data');
                    }
                } catch (err) {
                    console.error("Error in prayer time calculation:", err);
                    setCalculationState('calculation-error');

                    // Fallback - still show the prayer even if calculation failed
                    if (!nextPrayer) {
                        setNextPrayer(nextPrayerInfo);
                        if (nextPrayerInfo.dateTime) {
                            setRemainingTime(nextPrayerInfo.dateTime - now);
                        }
                        setProgress(0.5); // Default progress
                    }
                }
            } else {
                // No next prayer found - severe problem with the data
                setCalculationState('no-next-prayer');
                if (DEBUG) console.error("No next prayer could be determined");
            }
        } catch (error) {
            console.error("Error calculating prayer times:", error);
            setCalculationState('error');
            // Fall back to default/partial values if possible
            if (prayerTimes && prayerTimes.Fajr) {
                // Create a simple next prayer object
                const [hour, minute] = prayerTimes.Fajr.split(':').map(Number);
                if (!isNaN(hour) && !isNaN(minute)) {
                    const fajrTime = new Date();
                    fajrTime.setHours(hour, minute, 0, 0);

                    // If Fajr is in the past, set it to tomorrow
                    if (fajrTime < currentTime) {
                        fajrTime.setDate(fajrTime.getDate() + 1);
                    }

                    setNextPrayer({
                        name: 'Fajr',
                        time: prayerTimes.Fajr,
                        dateTime: fajrTime
                    });
                    setRemainingTime(fajrTime - currentTime);
                    setProgress(0.5); // Default progress
                    if (DEBUG) console.log("Using fallback values after error");
                }
            }
        }
    }, [prayerTimes, currentTime]);

    // Display loading or error state if data is not available
    if (!prayerTimes) {
        return (
            <Paper elevation={3} sx={{ p: 4, textAlign: 'center', mb: 4 }}>
                <Typography variant="h6" color="text.secondary">
                    {loading ? (
                        <>
                            <CircularProgress size={24} sx={{ mr: 2, verticalAlign: 'middle' }} />
                            Loading prayer times...
                        </>
                    ) : (
                        'Prayer times not available'
                    )}
                </Typography>
                {!loading && (
                    <Typography variant="body2" color="text.secondary" sx={{ mt: 2 }}>
                        Please set your location to view prayer times.
                    </Typography>
                )}
            </Paper>
        );
    }

    // Display loading for nextPrayer not being calculated yet
    if (!nextPrayer) {
        return (
            <Paper elevation={3} sx={{ p: 4, textAlign: 'center', mb: 4 }}>
                <CircularProgress size={24} sx={{ mr: 2, verticalAlign: 'middle' }} />
                <Typography variant="h6" color="text.secondary">
                    Calculating prayer times... ({calculationState})
                </Typography>
                {DEBUG && (
                    <Typography variant="body2" color="error" sx={{ mt: 2 }}>
                        Debug info: Prayer data received but calculation incomplete.
                        Check console for details.
                    </Typography>
                )}
            </Paper>
        );
    }

    // Format Islamic date for display
    const formattedIslamicDate = hijriDate ?
        `${hijriDate.day} ${hijriDate.month.en} ${hijriDate.year} AH` : '';

    // Get location name
    const locationName = params.address ||
        `${params.latitude.toFixed(4)}, ${params.longitude.toFixed(4)}`;

    // Main prayer times with icons
    const mainPrayers = [
        { name: 'Fajr', time: prayerTimes.Fajr, icon: '' },
        { name: 'Sunrise', time: prayerTimes.Sunrise, icon: '' },
        { name: 'Dhuhr', time: prayerTimes.Dhuhr, icon: '' },
        { name: 'Asr', time: prayerTimes.Asr, icon: '' },
        { name: 'Maghrib', time: prayerTimes.Maghrib, icon: '' },
        { name: 'Isha', time: prayerTimes.Isha, icon: '' }
    ];

    // Filter out prayers with no time
    const validPrayers = mainPrayers.filter(prayer => prayer && prayer.time);

    return (
        <Box sx={{ mb: 4 }}>
            {/* Primary layout grid */}
            <Grid container spacing={4}>
                {/* Left side: Countdown Clock */}
                <Grid item xs={12} md={6}>
                    <CountdownClock
                        targetTime={nextPrayer.dateTime}
                        title={`${nextPrayer.name} Prayer`}
                        subtitle={`at ${formatTime12Hour(nextPrayer.time)}`}
                        description={locationName}
                    />
                </Grid>

                {/* Right side: Next prayer clock */}
                <Grid item xs={12} md={6}>
                    <Card>
                        <CardContent sx={{ textAlign: 'center' }}>
                            <Typography variant="h5" color="primary" gutterBottom>
                                Next Prayer
                            </Typography>
                            <Box sx={{ maxWidth: 315, mx: 'auto', my: 2 }}>
                                <PrayerClock
                                    prayerTime={nextPrayer.dateTime}
                                    remaining={remainingTime}
                                    progress={progress}
                                    prayerName={nextPrayer.name}
                                    cityName={locationName}
                                    islamicDate={formattedIslamicDate}
                                    isCompact={false}
                                    isNearPrayer={remainingTime < 30 * 60 * 1000}
                                    clockSize={315}  // Adjusted size for better visibility
                                />
                            </Box>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>

            <Divider sx={{ my: 4 }} />

            {/* All prayer times clocks */}
            <Typography variant="h6" gutterBottom sx={{ color: theme.palette.primary.main, mb: 3 }}>
                Today's Prayer Times
            </Typography>

            <Grid container spacing={2}> {/* Reduced spacing from 3 to 2 */}
                {validPrayers.map((prayer) => {
                    try {
                        if (!prayer.time) return null;

                        const isObligatory = ['Fajr', 'Dhuhr', 'Asr', 'Maghrib', 'Isha'].includes(prayer.name);

                        const [hours, minutes] = prayer.time.split(':').map(Number);
                        if (isNaN(hours) || isNaN(minutes)) return null;

                        const prayerDateTime = new Date();
                        prayerDateTime.setHours(hours, minutes, 0, 0);

                        const isPassed = prayerDateTime < currentTime;
                        const isNext = nextPrayer?.name === prayer.name ||
                            (nextPrayer?.name === 'Fajr (Tomorrow)' && prayer.name === 'Fajr');

                        return (
                            <Grid
                                item
                                xs={6} // Change from xs={12} to xs={6} for 2 columns on mobile
                                sm={6}
                                md={4}
                                key={prayer.name}
                            >
                                <Card
                                    sx={{
                                        height: '100%',
                                        textAlign: 'center',
                                        transform: isNext ? 'scale(1.05)' : 'scale(1)',
                                        transition: 'transform 0.3s ease',
                                        opacity: isPassed && !isNext ? 0.7 : 1,
                                        border: isNext ? `1px solid ${theme.palette.secondary.main}` : 'none',
                                        boxShadow: isNext ? 3 : 1,
                                        bgcolor: isObligatory ? 'background.paper' : 'background.default',
                                        p: { xs: 1, sm: 2 }, // Smaller padding on mobile
                                        overflow: 'visible', // Change from hidden to visible
                                        boxSizing: 'border-box',
                                        mx: 'auto',
                                    }}
                                >
                                    <CardContent 
                                        sx={{ 
                                            p: { xs: 0.5, sm: 1 },
                                            '&:last-child': { pb: { xs: 0.5, sm: 1 } },
                                            display: 'flex',
                                            flexDirection: 'column',
                                            alignItems: 'center'
                                        }}
                                    >
                                        <Typography 
                                            variant="subtitle2" // Change from subtitle1 to subtitle2 on mobile
                                            sx={{ 
                                                mb: 0.5, // Reduce margin
                                                fontWeight: 'medium',
                                                fontSize: { xs: '0.7rem', sm: '0.9rem' } // Smaller on mobile
                                            }}
                                        >
                                            {prayer.icon} {prayer.name}
                                        </Typography>
                                        
                                        <Box 
                                            sx={{ 
                                                display: 'flex', 
                                                justifyContent: 'center',
                                                width: '100%',
                                                transform: { xs: 'scale(0.8)', sm: 'scale(1)' } // Scale down on mobile
                                            }}
                                        >
                                            <PrayerClock
                                                prayerTime={prayerDateTime}
                                                remaining={0}
                                                progress={0}
                                                prayerName={prayer.name}
                                                cityName={''}
                                                islamicDate={''}
                                                isCompact={true}
                                                isNearPrayer={isNext}
                                                clockSize={170} // Reduce size from 80 to 60
                                            />
                                        </Box>
                                        
                                        <Typography
                                            sx={{
                                                mt: 0.5, // Reduce top margin
                                                fontSize: { xs: '0.8rem', sm: '1rem' }, // Smaller font on mobile
                                                color: isNext ? theme.palette.secondary.main : theme.palette.text.primary,
                                                fontWeight: isNext ? 700 : 500
                                            }}
                                        >
                                            {formatTime12Hour(prayer.time)}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                        );
                    } catch (error) {
                        console.error(`Error rendering prayer: ${prayer.name}`, error);
                        return null; // Skip rendering this prayer if there's an error
                    }
                })}
            </Grid>
        </Box>
    );
};

export default EnhancedPrayerTimesDisplay;