import React, { useState, useEffect } from 'react';
import {
    Box,
    Card,
    CardContent,
    Typography,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Button,
    Grid
} from '@mui/material';
import {
    CALCULATION_METHODS,
    JURISTIC_METHODS,
    HIGH_LATITUDE_METHODS,
    MIDNIGHT_MODES
} from '../../utils/constants';
import { usePrayerTimes } from '../../contexts/PrayerTimesContext';

const CalculationMethodForm = ({ onSubmit }) => {
    const { params } = usePrayerTimes();

    // Initialize form state from context params
    const [formData, setFormData] = useState({
        method: params.method || 2, // Default to ISNA if not set
        school: params.school || 0, // Default to Standard
        latitudeAdjustmentMethod: params.latitudeAdjustmentMethod || 3, // Default to ANGLE_BASED (3)
        midnightMode: params.midnightMode || 0 // Default to STANDARD (0)
    });

    // Update form when params change externally
    useEffect(() => {
        setFormData({
            method: params.method || 2,
            school: params.school || 0,
            // Convert string values to numeric for MUI Select
            latitudeAdjustmentMethod: convertLatitudeMethodToNumeric(params.latitudeAdjustmentMethod),
            midnightMode: convertMidnightModeToNumeric(params.midnightMode)
        });
    }, [params]);

    // Helper function to convert string latitude method to numeric for MUI Select
    function convertLatitudeMethodToNumeric(methodString) {
        const methodMap = {
            'NONE': 0,
            'NIGHT_MIDDLE': 1,
            'ONE_SEVENTH': 2,
            'ANGLE_BASED': 3
        };
        return methodString && methodMap[methodString] !== undefined ? methodMap[methodString] : 3;
    }

    // Helper function to convert numeric latitude method back to string
    function convertLatitudeMethodToString(methodNumeric) {
        const methodMap = {
            0: 'NONE',
            1: 'NIGHT_MIDDLE',
            2: 'ONE_SEVENTH',
            3: 'ANGLE_BASED'
        };
        return methodNumeric !== undefined ? methodMap[methodNumeric] : 'ANGLE_BASED';
    }

    // Helper function to convert string midnight mode to numeric for MUI Select
    function convertMidnightModeToNumeric(modeString) {
        const modeMap = {
            'STANDARD': 0,
            'JAFARI': 1
        };
        return modeString && modeMap[modeString] !== undefined ? modeMap[modeString] : 0;
    }

    // Helper function to convert numeric midnight mode back to string
    function convertMidnightModeToString(modeNumeric) {
        const modeMap = {
            0: 'STANDARD',
            1: 'JAFARI'
        };
        return modeNumeric !== undefined ? modeMap[modeNumeric] : 'STANDARD';
    }

    const handleChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value
        });
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        // Convert numeric values back to strings for API
        const submissionData = {
            ...formData,
            latitudeAdjustmentMethod: convertLatitudeMethodToString(formData.latitudeAdjustmentMethod),
            midnightMode: convertMidnightModeToString(formData.midnightMode)
        };

        onSubmit(submissionData);
    };

    return (
        <Card>
            <CardContent>
                <Typography variant="h6" gutterBottom>
                    Calculation Settings
                </Typography>

                <Box component="form" onSubmit={handleSubmit}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <FormControl fullWidth margin="normal">
                                <InputLabel id="method-label">Calculation Method</InputLabel>
                                <Select
                                    labelId="method-label"
                                    id="method"
                                    name="method"
                                    value={formData.method}
                                    label="Calculation Method"
                                    onChange={handleChange}
                                >
                                    {CALCULATION_METHODS.map((method) => (
                                        <MenuItem key={method.id} value={method.id}>
                                            {method.name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth margin="normal">
                                <InputLabel id="school-label">Juristic Method</InputLabel>
                                <Select
                                    labelId="school-label"
                                    id="school"
                                    name="school"
                                    value={formData.school}
                                    label="Juristic Method"
                                    onChange={handleChange}
                                >
                                    {JURISTIC_METHODS.map((method) => (
                                        <MenuItem key={method.id} value={method.id}>
                                            {method.name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth margin="normal">
                                <InputLabel id="latitude-method-label">High Latitude Method</InputLabel>
                                <Select
                                    labelId="latitude-method-label"
                                    id="latitudeAdjustmentMethod"
                                    name="latitudeAdjustmentMethod"
                                    value={formData.latitudeAdjustmentMethod}
                                    label="High Latitude Method"
                                    onChange={handleChange}
                                >
                                    {/* Use numeric IDs for MUI Select */}
                                    <MenuItem value={0}>None</MenuItem>
                                    <MenuItem value={1}>Middle of Night</MenuItem>
                                    <MenuItem value={2}>1/7th of Night</MenuItem>
                                    <MenuItem value={3}>Angle Based</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth margin="normal">
                                <InputLabel id="midnight-mode-label">Midnight Mode</InputLabel>
                                <Select
                                    labelId="midnight-mode-label"
                                    id="midnightMode"
                                    name="midnightMode"
                                    value={formData.midnightMode}
                                    label="Midnight Mode"
                                    onChange={handleChange}
                                >
                                    {/* Use numeric IDs for MUI Select */}
                                    <MenuItem value={0}>Standard (Mid Sunset to Sunrise)</MenuItem>
                                    <MenuItem value={1}>Jafari (Mid Sunset to Fajr)</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>

                        <Grid item xs={12}>
                            <Box sx={{ mt: 2 }}>
                                <Button
                                    type="submit"
                                    variant="contained"
                                    color="primary"
                                    fullWidth
                                >
                                    Apply Settings
                                </Button>
                            </Box>
                        </Grid>
                    </Grid>
                </Box>
            </CardContent>
        </Card>
    );
};

export default CalculationMethodForm;