/**
 * Format coordinates to a readable string
 * @param {number} latitude - Latitude
 * @param {number} longitude - Longitude
 * @param {number} decimals - Number of decimal places
 * @returns {string} - Formatted coordinates
 */
export const formatCoordinates = (latitude, longitude, decimals = 4) => {
   if (latitude === undefined || longitude === undefined) {
      return '';
   }

   const lat = parseFloat(latitude).toFixed(decimals);
   const lng = parseFloat(longitude).toFixed(decimals);

   const latDir = lat >= 0 ? 'N' : 'S';
   const lngDir = lng >= 0 ? 'E' : 'W';

   return `${Math.abs(lat)} ${latDir}, ${Math.abs(lng)} ${lngDir}`;
};

/**
 * Formats a time string in 12-hour format
 * @param {string} time - Time string in 24-hour format (HH:MM)
 * @returns {string} Time in 12-hour format with AM/PM
 */
export const formatTime12Hour = (time) => {
   if (!time) return '';

   const [hours, minutes] = time.split(':').map(Number);

   let period = 'AM';
   let displayHours = hours;

   if (hours >= 12) {
      period = 'PM';
      if (hours > 12) {
         displayHours = hours - 12;
      }
   }

   if (displayHours === 0) {
      displayHours = 12;
   }

   return `${displayHours}:${String(minutes).padStart(2, '0')} ${period}`;
};

/**
 * Formats a time string in 24-hour format
 * @param {string} time - Time string in 24-hour format (HH:MM)
 * @returns {string} Time in 24-hour format with leading zeros
 */
export const formatTime24Hour = (time) => {
   if (!time) return '';

   const [hours, minutes] = time.split(':').map(Number);
   return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
};

/**
 * Formats milliseconds into hours, minutes, seconds
 * @param {number} ms - Milliseconds
 * @returns {string} Formatted time string (e.g., "2h 30m 15s")
 */
export const formatTimeRemaining = (ms) => {
   if (ms <= 0) return "0m 0s";

   const seconds = Math.floor((ms / 1000) % 60);
   const minutes = Math.floor((ms / (1000 * 60)) % 60);
   const hours = Math.floor(ms / (1000 * 60 * 60));

   let result = '';
   if (hours > 0) result += `${hours}h `;
   if (hours > 0 || minutes > 0) result += `${minutes}m `;
   result += `${seconds}s`;

   return result;
};

/**
 * Format timezone to a readable string
 * @param {string} timezone - Timezone identifier (e.g., 'America/New_York')
 * @returns {string} - Formatted timezone
 */
export const formatTimezone = (timezone) => {
   if (!timezone) return '';

   // Try to format the timezone in a more readable way
   try {
      // Remove continent/region prefix and replace underscores with spaces
      return timezone.split('/').pop().replace(/_/g, ' ');
   } catch (e) {
      return timezone;
   }
};

