/**
 * Format a number with leading zero if needed
 * @param {number} num - Number to format
 * @returns {string} - Formatted number with leading zero if < 10
 */
export const padZero = (num) => {
   return num < 10 ? `0${num}` : num.toString();
};

/**
 * Format bytes to a human-readable string
 * @param {number} bytes - Number of bytes
 * @param {number} decimals - Number of decimal places
 * @returns {string} - Formatted bytes (e.g., "1.5 MB")
 */
export const formatBytes = (bytes, decimals = 2) => {
   if (bytes === 0) return '0 Bytes';

   const k = 1024;
   const dm = decimals < 0 ? 0 : decimals;
   const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

   const i = Math.floor(Math.log(bytes) / Math.log(k));

   return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
};

/**
 * Format minutes to hours and minutes string
 * @param {number} totalMinutes - Minutes to format
 * @returns {string} - Formatted time (e.g., "1h 30m")
 */
export const formatMinutesToHoursAndMinutes = (totalMinutes) => {
   if (!totalMinutes && totalMinutes !== 0) return '';

   // Handle negative minutes
   const isNegative = totalMinutes < 0;
   const absMinutes = Math.abs(totalMinutes);

   const hours = Math.floor(absMinutes / 60);
   const minutes = absMinutes % 60;

   let result = '';

   if (hours > 0) {
      result += `${hours}h `;
   }

   result += `${minutes}m`;

   return isNegative ? `-${result}` : result;
};