import React, { useRef, useEffect, useState } from 'react';
import { Box, Typography } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import { formatTime12Hour } from '../../utils/formatters';

const PrayerClock = ({
    prayerTime,
    remaining,
    progress,
    prayerName,
    cityName,
    islamicDate,
    isCompact = false,
    isNearPrayer = false,
    clockSize = 180,
}) => {
    const theme = useTheme();
    const canvasRef = useRef(null);
    const [currentTime, setCurrentTime] = useState(new Date());
    const [fadeIn, setFadeIn] = useState(0);
    const [rotation, setRotation] = useState(-0.1);
    const [scale, setScale] = useState(isCompact ? 1 : 0.8);

    // Animation frame reference
    const animationRef = useRef();

    // Set up the clock and animations
    useEffect(() => {
        // Start fade-in animation
        const fadeTimer = setTimeout(() => {
            setFadeIn(1);
            setRotation(0);
        }, 100);

        // Set up pulse animation for large clock
        if (!isCompact) {
            let direction = 1;
            let currentScale = 1;

            const animatePulse = () => {
                if (currentScale >= 1.2) direction = -1;
                if (currentScale <= 0.8) direction = 1;

                currentScale += direction * 0.005;
                setScale(currentScale);

                animationRef.current = requestAnimationFrame(animatePulse);
            };

            animationRef.current = requestAnimationFrame(animatePulse);
        }

        // Update the current time every second
        const timer = !isCompact ? setInterval(() => {
            setCurrentTime(new Date());
        }, 1000) : null;

        // Clean up animations and timer
        return () => {
            clearTimeout(fadeTimer);
            if (timer) clearInterval(timer);
            if (animationRef.current) cancelAnimationFrame(animationRef.current);
        };
    }, [isCompact]);

    // Update the useEffect for drawing the clock
    useEffect(() => {
        const canvas = canvasRef.current;
        if (!canvas) return;

        const ctx = canvas.getContext('2d');
        const timeToShow = isCompact ? new Date(prayerTime) : currentTime;

        // Set canvas size based on clockSize prop and device pixel ratio
        const dpr = window.devicePixelRatio || 1;
        // Use clockSize directly here instead of hardcoded values
        const size = clockSize;
        canvas.width = size * dpr;
        canvas.height = size * dpr;
        ctx.scale(dpr, dpr);
        canvas.style.width = `${size}px`;
        canvas.style.height = `${size}px`;

        // Clear canvas
        ctx.clearRect(0, 0, canvas.width, canvas.height);

        // Draw clock
        drawClock(ctx, size, timeToShow, theme.palette.primary.main, theme.palette.divider);

        // Draw progress ring
        drawProgressRing(ctx, size, progress, theme.palette.primary.main, theme.palette.divider);

    }, [currentTime, prayerTime, progress, isCompact, isNearPrayer, theme, clockSize]); // Add clockSize to deps

    // Helper function to draw the clock face
    const drawClock = (ctx, size, time, color, bgColor) => {
        const center = { x: size / 2, y: size / 2 };
        const radius = (size / 2) - (size * 0.02); // Scale border with size
        
        // Scale lineWidths with size
        const mainLineWidth = size * 0.02;
        const hourLineWidth = size * 0.02;
        const minuteLineWidth = size * 0.01;
        const secondLineWidth = size * 0.005;
        
        // Draw clock face
        ctx.beginPath();
        ctx.arc(center.x, center.y, radius, 0, 2 * Math.PI);
        ctx.strokeStyle = bgColor;
        ctx.lineWidth = mainLineWidth;
        ctx.stroke();
        
        // Draw hour markers - scale with size
        for (let i = 0; i < 12; i++) {
            const angle = i * Math.PI / 6;
            const isMainMarker = i % 3 === 0;
            
            ctx.beginPath();
            ctx.strokeStyle = isMainMarker ? color : `${color}B3`;
            ctx.lineWidth = isMainMarker ? size * 0.015 : size * 0.005;
            
            const markerLength = isMainMarker ? size * 0.08 : size * 0.04;
            const outerX = center.x + (radius - size * 0.04) * Math.cos(angle);
            const outerY = center.y + (radius - size * 0.04) * Math.sin(angle);
            const innerX = center.x + (radius - size * 0.04 - markerLength) * Math.cos(angle);
            const innerY = center.y + (radius - size * 0.04 - markerLength) * Math.sin(angle);
            
            ctx.moveTo(innerX, innerY);
            ctx.lineTo(outerX, outerY);
            ctx.stroke();
        }
        
        // Draw hour hand - scale with size
        const hours = time.getHours() % 12;
        const minutes = time.getMinutes();
        const hourAngle = (hours + minutes / 60) * Math.PI / 6 - Math.PI / 2;
        
        ctx.beginPath();
        ctx.lineWidth = hourLineWidth;
        ctx.strokeStyle = color;
        ctx.moveTo(center.x, center.y);
        ctx.lineTo(
            center.x + radius * 0.4 * Math.cos(hourAngle),
            center.y + radius * 0.4 * Math.sin(hourAngle)
        );
        ctx.stroke();
        
        // Draw minute hand - scale with size
        const minuteAngle = minutes * Math.PI / 30 - Math.PI / 2;
        
        ctx.beginPath();
        ctx.lineWidth = minuteLineWidth;
        ctx.strokeStyle = color;
        ctx.moveTo(center.x, center.y);
        ctx.lineTo(
            center.x + radius * 0.6 * Math.cos(minuteAngle),
            center.y + radius * 0.6 * Math.sin(minuteAngle)
        );
        ctx.stroke();
        
        // Rest of the function remains similar but with scaled values
        const seconds = time.getSeconds();
        const secondAngle = seconds * Math.PI / 30 - Math.PI / 2;

        // Draw glow effect for seconds hand
        ctx.beginPath();
        ctx.lineWidth = secondLineWidth * 2;
        ctx.strokeStyle = `${color}4D`; // 30% opacity
        ctx.lineCap = 'round';
        ctx.shadowColor = color;
        ctx.shadowBlur = size * 0.02;
        ctx.moveTo(center.x, center.y);
        ctx.lineTo(
            center.x + radius * 0.7 * Math.cos(secondAngle),
            center.y + radius * 0.7 * Math.sin(secondAngle)
        );
        ctx.stroke();
        ctx.shadowBlur = 0; // Reset shadow

        // Draw the actual second hand
        ctx.beginPath();
        ctx.lineWidth = secondLineWidth;
        ctx.strokeStyle = color;
        ctx.moveTo(center.x, center.y);
        ctx.lineTo(
            center.x + radius * 0.7 * Math.cos(secondAngle),
            center.y + radius * 0.7 * Math.sin(secondAngle)
        );
        ctx.stroke();

        // Draw glowing center dot
        ctx.beginPath();
        ctx.fillStyle = `${color}66`; // 40% opacity
        ctx.shadowColor = color;
        ctx.shadowBlur = size * 0.02;
        ctx.arc(center.x, center.y, size * 0.02, 0, 2 * Math.PI);
        ctx.fill();
        ctx.shadowBlur = 0; // Reset shadow

        // Draw solid center dot - scale with size
        ctx.beginPath();
        ctx.fillStyle = color;
        ctx.arc(center.x, center.y, isCompact ? size * 0.01 : size * 0.015, 0, 2 * Math.PI);
        ctx.fill();
    };

    // Helper function to draw the progress ring - scale with size
    const drawProgressRing = (ctx, size, progress, color, bgColor) => {
        const center = { x: size / 2, y: size / 2 };
        const radius = (size / 2) - (isCompact ? size * 0.02 : size * 0.04);
        const strokeWidth = isCompact ? size * 0.02 : size * 0.04;

        // Draw background circle
        ctx.beginPath();
        ctx.arc(center.x, center.y, radius, 0, 2 * Math.PI);
        ctx.strokeStyle = `${bgColor}1A`; // 10% opacity
        ctx.lineWidth = strokeWidth;
        ctx.stroke();

        // Create gradient for progress arc
        const gradient = ctx.createLinearGradient(
            center.x - radius, center.y,
            center.x + radius, center.y
        );
        gradient.addColorStop(0, `${color}B3`); // 70% opacity
        gradient.addColorStop(1, color);

        // Draw progress arc
        ctx.beginPath();
        ctx.arc(
            center.x, center.y, radius,
            -Math.PI / 2, // Start at the top
            -Math.PI / 2 + (2 * Math.PI * progress), // End based on progress
            false // Counter-clockwise = false
        );
        ctx.strokeStyle = gradient;
        ctx.lineWidth = strokeWidth;
        ctx.lineCap = 'round';
        ctx.stroke();
    };

    // Helper function to render time boxes (hours, minutes)
    const renderTimeBox = (value) => {
        // Calculate size based on clockSize but with minimum/maximum boundaries
        const fontSize = isCompact ? 
            Math.min(Math.max(clockSize * 0.09, 9), 14) : // Min 9px, max 14px for compact
            Math.min(Math.max(clockSize * 0.12, 12), 20); // Min 12px, max 20px for full
            
        const padding = isCompact ? 
            `${Math.min(Math.max(clockSize * 0.01, 2), 4)}px ${Math.min(Math.max(clockSize * 0.02, 4), 8)}px` : 
            `${Math.min(Math.max(clockSize * 0.013, 3), 6)}px ${Math.min(Math.max(clockSize * 0.027, 6), 12)}px`;

        const borderRadius = isCompact ? 
            Math.min(Math.max(clockSize * 0.01, 2), 4) : 
            Math.min(Math.max(clockSize * 0.02, 4), 8);

        return (
            <Box
                sx={{
                    padding,
                    backgroundColor: `${theme.palette.primary.main}1A`,
                    borderRadius,
                    display: 'inline-block'
                }}
            >
                <Typography
                    sx={{
                        color: theme.palette.text.primary,
                        fontSize,
                        fontWeight: 'bold',
                        lineHeight: 1,
                    }}
                >
                    {String(value).padStart(2, '0')}
                </Typography>
            </Box>
        );
    };

    // Format remaining time for display
    const formatRemaining = () => {
        const hours = Math.floor(remaining / 3600000);
        const minutes = Math.floor((remaining % 3600000) / 60000);
        return `${hours}h ${minutes}m`;
    };

    const timeToShow = isCompact ? new Date(prayerTime) : currentTime;

    return (
        <Box
            sx={{
                opacity: fadeIn,
                transform: `rotate(${rotation}deg) scale(${scale})`,
                transition: 'opacity 0.5s ease, transform 0.5s ease-out',
                position: 'relative',
                width: clockSize,
                height: clockSize,
                margin: '0 auto',
                
            }}
        >
            
            {/* Clock Canvas */}
            <canvas
                ref={canvasRef}
                style={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    width: clockSize,
                    height: clockSize,
                }}
            />

            {/* Digital Time and Info */}
            <Box
                sx={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    textAlign: 'center',
                    width: clockSize * 0.8, // Scale with clock size
                    maxWidth: '90%', // Prevent overflow
                }}
            >
                {!isCompact && (
                    <>
                        <Typography
                            variant="caption"
                            sx={{
                                color: `${theme.palette.text.secondary}B3`,
                                fontSize: clockSize * 0.045, // Scale with clock size
                                display: 'block',
                            }}
                        >
                            {islamicDate}
                        </Typography>
                        <Typography
                            variant="caption"
                            sx={{
                                color: `${theme.palette.text.secondary}B3`,
                                fontSize: clockSize * 0.055, // Scale with clock size
                                fontWeight: 500,
                                display: 'block',
                                mb: clockSize * 0.008, // Scale with clock size
                            }}
                        >
                            {cityName}
                        </Typography>
                    </>
                )}
                

                {/* Digital Time Display */}
                <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                    {renderTimeBox(timeToShow.getHours())}
                    <Typography
                        sx={{
                            color: theme.palette.text.primary,
                            fontSize: isCompact ? clockSize * 0.11 : clockSize * 0.14, // Scale with clock size
                            mx: 0.5,
                            lineHeight: 1,
                        }}
                    >:</Typography>
                    {renderTimeBox(timeToShow.getMinutes())}
                </Box>

                {/* Prayer Name or Countdown */}
                {isCompact ? (
                    <Typography
                        variant="body2"
                        sx={{
                            color: `${theme.palette.text.primary}E6`,
                            fontSize: clockSize * 0.067, // Scale with clock size
                            fontWeight: 500,
                            mt: 0.5,
                        }}
                    >
                        {prayerName}
                    </Typography>
                ) : (
                    <Typography
                        variant="body2"
                        sx={{
                            color: `${theme.palette.text.secondary}B3`,
                            fontSize: clockSize * 0.067, // Scale with clock size
                            mt: 0.5,
                        }}
                    >
                        {formatRemaining()}
                    </Typography>
                )}
            </Box>
        </Box>
    );
};

export default PrayerClock;