import React, { useContext, useState } from 'react';
import {
  Box,
  Typography,
  Switch,
  FormControlLabel,
  Slider,
  Button,
  Alert,
  Paper,
  Divider,
  FormControl,
  FormLabel,
  Select,
  MenuItem,
  Snackbar,
  Tooltip,
  IconButton
} from '@mui/material';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import VolumeDownIcon from '@mui/icons-material/VolumeDown';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import StopIcon from '@mui/icons-material/Stop';
import NotificationsIcon from '@mui/icons-material/Notifications';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import { AthanSettingsContext } from '../contexts/AthanSettingsContext';
import PersonalizedReminders from './PersonalizedReminders';

const AthanSettings = () => {
  const {
    settings,
    updateSettings,
    requestNotificationPermission,
    playAthan,
    stopAthan,
    availableAthans,
    availableFajrAthans
  } = useContext(AthanSettingsContext);
  
  const [message, setMessage] = useState(null);
  const [showSnackbar, setShowSnackbar] = useState(false);

  // Main toggle handler - handles both enabled and notification permission
  const handleMainToggle = async (e) => {
    const isEnabled = e.target.checked;
    
    // If turning off, just disable everything
    if (!isEnabled) {
      updateSettings({ 
        enabled: false,
        notificationsEnabled: false 
      });
      return;
    }
    
    // If turning on
    updateSettings({ enabled: true });
    
    // Request notification permission if not already granted
    if (!settings.notificationsEnabled) {
      const result = await requestNotificationPermission();
      setMessage(result.message);
      setShowSnackbar(true);
    }
  };

  // Handle volume change
  const handleVolumeChange = (event, newValue) => {
    updateSettings({ volume: newValue });
  };

  // Handle playing test sound
  const handlePlayAthan = (isFajr = false) => {
    if (settings.playingAudio) {
      stopAthan();
    } else {
      playAthan(isFajr);
    }
  };

  // Handle Athan selection change
  const handleAthanChange = (event) => {
    const selectedAthan = availableAthans.find(athan => athan.id === event.target.value);
    if (selectedAthan) {
      updateSettings({ athanPath: selectedAthan.url });
    }
  };

  // Handle Fajr Athan selection change
  const handleFajrAthanChange = (event) => {
    const selectedAthan = availableFajrAthans.find(athan => athan.id === event.target.value);
    if (selectedAthan) {
      updateSettings({ fajrAthanPath: selectedAthan.url });
    }
  };

  // Get current Athan ID from path
  const getCurrentAthanId = () => {
    const current = availableAthans.find(athan => athan.url === settings.athanPath);
    return current ? current.id : 'makkah';
  };

  // Get current Fajr Athan ID from path
  const getCurrentFajrAthanId = () => {
    const current = availableFajrAthans.find(athan => athan.url === settings.fajrAthanPath);
    return current ? current.id : 'mansour-fajr';
  };

  return (
    <Paper elevation={2} sx={{ p: 3, borderRadius: 2, mb: 3 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h5" component="h2" gutterBottom sx={{ display: 'flex', alignItems: 'center', mb: 0 }}>
          <NotificationsIcon sx={{ mr: 1 }} />
          Athan Settings
        </Typography>
        <Tooltip title="Athan will play automatically at prayer times. Browser notifications will appear if you allow them.">
          <IconButton size="small">
            <HelpOutlineIcon fontSize="small" />
          </IconButton>
        </Tooltip>
      </Box>
      
      <Divider sx={{ my: 2 }} />
      
      {/* Single Main Toggle for Athan */}
      <FormControlLabel
        control={
          <Switch
            checked={settings.enabled}
            onChange={handleMainToggle}
            color="primary"
          />
        }
        label={
          <Box component="span" sx={{ display: 'flex', alignItems: 'center' }}>
            Enable Athan
            {settings.notificationsEnabled && (
              <Box 
                component="span" 
                sx={{ 
                  ml: 1, 
                  fontSize: '0.75rem', 
                  bgcolor: 'success.main',
                  color: 'white',
                  py: 0.25,
                  px: 1,
                  borderRadius: 1
                }}
              >
                With Notifications
              </Box>
            )}
          </Box>
        }
        sx={{ mb: 2, display: 'block' }}
      />
      
      {settings.enabled && (
        <Box sx={{ pl: 1 }}>
          {/* Special Fajr Athan Toggle */}
          <FormControlLabel
            control={
              <Switch
                checked={settings.specialFajrAthan}
                onChange={(e) => updateSettings({ specialFajrAthan: e.target.checked })}
                color="primary"
              />
            }
            label="Use Special Fajr Athan"
            sx={{ mb: 3, display: 'block' }}
          />
          
          {/* Volume Control */}
          <Box sx={{ mb: 3 }}>
            <Typography id="athan-volume-slider" gutterBottom>
              Athan Volume
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <VolumeDownIcon sx={{ mr: 1 }} />
              <Slider
                value={settings.volume}
                onChange={handleVolumeChange}
                aria-labelledby="athan-volume-slider"
                valueLabelDisplay="auto"
                step={10}
                marks
                min={0}
                max={100}
                sx={{ mx: 1 }}
              />
              <VolumeUpIcon sx={{ ml: 1 }} />
            </Box>
          </Box>
          
          {/* Athan Selection */}
          <FormControl fullWidth sx={{ mb: 3 }}>
            <FormLabel>Regular Athan Sound</FormLabel>
            <Select
              value={getCurrentAthanId()}
              onChange={handleAthanChange}
              size="small"
              sx={{ mt: 1 }}
            >
              {availableAthans.map(athan => (
                <MenuItem key={athan.id} value={athan.id}>
                  {athan.name}
                </MenuItem>
              ))}
            </Select>
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 1 }}>
              <Button
                variant="outlined"
                size="small"
                startIcon={settings.playingAudio && !settings.specialFajrAthan ? <StopIcon /> : <PlayArrowIcon />}
                onClick={() => handlePlayAthan(false)}
                sx={{ minWidth: 100 }}
              >
                {settings.playingAudio && !settings.specialFajrAthan ? 'Stop' : 'Test'}
              </Button>
            </Box>
          </FormControl>
          
          {/* Fajr Athan Selection (if special Fajr enabled) */}
          {settings.specialFajrAthan && (
            <FormControl fullWidth sx={{ mb: 3 }}>
              <FormLabel>Fajr Athan Sound</FormLabel>
              <Select
                value={getCurrentFajrAthanId()}
                onChange={handleFajrAthanChange}
                size="small"
                sx={{ mt: 1 }}
              >
                {availableFajrAthans.map(athan => (
                  <MenuItem key={athan.id} value={athan.id}>
                    {athan.name}
                  </MenuItem>
                ))}
              </Select>
              <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 1 }}>
                <Button
                  variant="outlined"
                  size="small"
                  startIcon={settings.playingAudio && settings.specialFajrAthan ? <StopIcon /> : <PlayArrowIcon />}
                  onClick={() => handlePlayAthan(true)}
                  sx={{ minWidth: 100 }}
                >
                  {settings.playingAudio && settings.specialFajrAthan ? 'Stop' : 'Test'}
                </Button>
              </Box>
            </FormControl>
          )}
          
          {/* Add Personalized Reminders component */}
          <PersonalizedReminders />
          
          {/* Info Alert */}
          <Alert severity="info" variant="outlined" sx={{ mt: 2 }}>
            Athan will play automatically at prayer times even when browsing other pages.
            {!settings.notificationsEnabled && (
              <Box sx={{ mt: 1 }}>
                <Button 
                  size="small" 
                  color="info" 
                  variant="contained" 
                  onClick={async () => {
                    const result = await requestNotificationPermission();
                    setMessage(result.message);
                    setShowSnackbar(true);
                  }}
                >
                  Enable Browser Notifications
                </Button>
              </Box>
            )}
          </Alert>
        </Box>
      )}
      
      {/* Notification Message Snackbar */}
      <Snackbar
        open={showSnackbar}
        autoHideDuration={6000}
        onClose={() => setShowSnackbar(false)}
        message={message}
      />
    </Paper>
  );
};

export default AthanSettings;