import React, { useContext } from 'react';
import {
  Box,
  Typography,
  Switch,
  FormControlLabel,
  Slider,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Grid,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import { AthanSettingsContext } from '../contexts/AthanSettingsContext';

const PersonalizedReminders = () => {
  const { settings, updateSettings } = useContext(AthanSettingsContext);
  
  // Prayer names to display
  const prayers = [
    { id: 'fajr', name: 'Fajr', defaultMinutes: 20, minMinutes: 5, maxMinutes: 60 },
    { id: 'dhuhr', name: 'Dhuhr', defaultMinutes: 10, minMinutes: 5, maxMinutes: 30 },
    { id: 'asr', name: 'Asr', defaultMinutes: 10, minMinutes: 5, maxMinutes: 30 },
    { id: 'maghrib', name: 'Maghrib', defaultMinutes: 10, minMinutes: 5, maxMinutes: 30 },
    { id: 'isha', name: 'Isha', defaultMinutes: 10, minMinutes: 5, maxMinutes: 30 }
  ];

  // Handle main toggle for personalized reminders
  const handleRemindersToggle = (event) => {
    updateSettings({ customRemindersEnabled: event.target.checked });
  };

  // Handle changing reminder time for a specific prayer
  const handleReminderTimeChange = (prayer, newValue) => {
    updateSettings({
      customReminderTimes: {
        ...settings.customReminderTimes,
        [prayer]: newValue
      }
    });
  };

  // Get current reminder time for a prayer
  const getReminderTime = (prayer) => {
    return settings.customReminderTimes?.[prayer] || 
      prayers.find(p => p.id === prayer)?.defaultMinutes || 10;
  };

  return (
    <Box sx={{ mt: 3 }}>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="personalized-reminders-content"
          id="personalized-reminders-header"
          sx={{ 
            bgcolor: settings.customRemindersEnabled ? 'primary.50' : 'inherit',
            '&.Mui-expanded': {
              bgcolor: settings.customRemindersEnabled ? 'primary.50' : 'inherit'
            }
          }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <AccessTimeIcon sx={{ mr: 1, color: 'primary.main' }} />
            <Typography>Personalized Prayer Reminders</Typography>
          </Box>
        </AccordionSummary>
        
        <AccordionDetails>
          <FormControlLabel
            control={
              <Switch
                checked={settings.customRemindersEnabled}
                onChange={handleRemindersToggle}
                color="primary"
              />
            }
            label="Enable pre-prayer reminders"
            sx={{ mb: 2, display: 'block' }}
          />
          
          {settings.customRemindersEnabled && (
            <Grid container spacing={2}>
              {prayers.map((prayer) => (
                <Grid item xs={12} key={prayer.id}>
                  <Typography id={`${prayer.id}-reminder-slider`} gutterBottom>
                    {prayer.name} prayer reminder: {getReminderTime(prayer.id)} minutes before
                  </Typography>
                  <Slider
                    value={getReminderTime(prayer.id)}
                    onChange={(_, newValue) => handleReminderTimeChange(prayer.id, newValue)}
                    aria-labelledby={`${prayer.id}-reminder-slider`}
                    valueLabelDisplay="auto"
                    step={5}
                    marks={[
                      { value: prayer.minMinutes, label: `${prayer.minMinutes}m` },
                      { value: prayer.defaultMinutes, label: `${prayer.defaultMinutes}m` },
                      { value: prayer.maxMinutes, label: `${prayer.maxMinutes}m` }
                    ]}
                    min={prayer.minMinutes}
                    max={prayer.maxMinutes}
                  />
                </Grid>
              ))}
            </Grid>
          )}
        </AccordionDetails>
      </Accordion>
    </Box>
  );
};

export default PersonalizedReminders;