import React, { useState, useEffect, useMemo, useRef } from 'react';
import { Helmet } from 'react-helmet-async';
import {
    Box, Grid, Typography, Container, Alert, Tabs, Tab,
    Button, Dialog, DialogTitle, DialogContent, DialogActions, CircularProgress,
    FormControlLabel, Switch, Snackbar
} from '@mui/material';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import { usePrayerTimes } from '../contexts/PrayerTimesContext';
import useGeolocation from '../hooks/useGeolocation';
import LocationForm from '../components/prayer-times/LocationForm';
import CalculationMethodForm from '../components/prayer-times/CalculationMethodForm';
import PrayerTimesDisplay from '../components/prayer-times/PrayerTimesDisplay';
import EnhancedPrayerTimesDisplay from '../components/prayer-times/EnhancedPrayerTimesDisplay';
import PrayerTimeline from '../components/prayer-times/PrayerTimeline';
import AthanPlayer from '../components/prayer-times/AthanPlayer';
import AthanSettings from '../components/AthanSettings';

const PrayerTimesPage = () => {
    const [displayMode, setDisplayMode] = useState('clocks');
    const [showLocationDialog, setShowLocationDialog] = useState(false);
    const [locationRequestInProgress, setLocationRequestInProgress] = useState(false);
    const [openPermissionInstructions, setOpenPermissionInstructions] = useState(false);
    const [keepPageActive, setKeepPageActive] = useState(() => {
        return localStorage.getItem('keepPrayerTimesActive') === 'true';
    });
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [snackbarMessage, setSnackbarMessage] = useState('');
    const previousLocationRef = useRef(null);
    const timerWorkerRef = useRef(null);
    const serviceWorkerRegistrationRef = useRef(null);
    const athanAudioRef = useRef(null);
    const lastWorkerPingRef = useRef(Date.now());
    const athanSettingsRef = useRef({});
    const wakeupTimeoutRef = useRef(null);
    const nextPrayerRef = useRef(null);

    const {
        prayerTimes,
        loading: prayerTimesLoading,
        error: prayerTimesError,
        params,
        updateParams,
        fetchPrayerTimesByCoordinates,
        fetchPrayerTimesByAddress,
        locationRequested
    } = usePrayerTimes();

    const {
        location,
        error: locationError,
        loading: locationLoading,
        getCurrentPosition,
        permissionState,
        DEFAULT_LOCATION
    } = useGeolocation();

    const isLoading = useMemo(() =>
        prayerTimesLoading || locationLoading || locationRequestInProgress,
        [prayerTimesLoading, locationLoading, locationRequestInProgress]
    );

    useEffect(() => {
        if ('serviceWorker' in navigator) {
            navigator.serviceWorker.register('/service-worker.js')
                .then(registration => {
                    console.log('Service Worker registered:', registration);
                    serviceWorkerRegistrationRef.current = registration;
                    navigator.serviceWorker.addEventListener('message', handleServiceWorkerMessage);
                })
                .catch(error => {
                    console.error('Service Worker registration failed:', error);
                });

            if ('Notification' in window) {
                Notification.requestPermission().then(permission => {
                    if (permission === 'granted') {
                        console.log('Notification permission granted');
                    }
                });
            }
        }

        return () => {
            if (navigator.serviceWorker) {
                navigator.serviceWorker.removeEventListener('message', handleServiceWorkerMessage);
            }
        };
    }, []);

    const handleServiceWorkerMessage = (event) => {
        if (event.data && event.data.type === 'PRAYER_NOTIFICATION_CLICK') {
            console.log('Notification clicked for prayer:', event.data.prayer);
            if (params.latitude && params.longitude) {
                fetchPrayerTimesByCoordinates({
                    latitude: params.latitude,
                    longitude: params.longitude
                });
            }
            if (athanAudioRef.current && nextPrayerRef.current?.name === event.data.prayer) {
                playAthan(event.data.prayer);
            }
        }
    };

    useEffect(() => {
        if (!locationRequested && permissionState === 'prompt') {
            setShowLocationDialog(true);
        }
    }, [locationRequested, permissionState]);

    useEffect(() => {
        if (!location || locationRequestInProgress || prayerTimesLoading) {
            return;
        }

        const locationString = `${location.latitude},${location.longitude}`;

        if (previousLocationRef.current === locationString) {
            return;
        }

        previousLocationRef.current = locationString;

        setLocationRequestInProgress(true);

        updateParams({
            latitude: location.latitude,
            longitude: location.longitude,
            address: location.name || `${location.latitude.toFixed(4)}, ${location.longitude.toFixed(4)}`
        });

        fetchPrayerTimesByCoordinates({
            latitude: location.latitude,
            longitude: location.longitude
        }).finally(() => {
            setLocationRequestInProgress(false);
        });
    }, [
        location,
        fetchPrayerTimesByCoordinates,
        updateParams,
        locationRequestInProgress,
        prayerTimesLoading
    ]);

    useEffect(() => {
        if (prayerTimes && prayerTimes.length > 0 && navigator.serviceWorker.controller) {
            navigator.serviceWorker.controller.postMessage({
                type: 'PRAYER_TIMES_UPDATE',
                prayerTimes
            });

            if (timerWorkerRef.current) {
                timerWorkerRef.current.postMessage({
                    command: 'updatePrayerTimes',
                    prayerTimes
                });
            }

            const now = new Date();
            nextPrayerRef.current = prayerTimes.find(prayer => new Date(prayer.dateTime) > now);
        }
    }, [prayerTimes]);

    const handleLocationSubmit = (locationData) => {
        if (locationRequestInProgress || prayerTimesLoading) return;

        if (locationData.type === 'coordinates') {
            fetchPrayerTimesByCoordinates({
                latitude: locationData.data.latitude,
                longitude: locationData.data.longitude,
                date: locationData.date
            });
        } else {
            fetchPrayerTimesByAddress(
                locationData.data.address,
                { date: locationData.date }
            );
        }
    };

    const handleMethodSubmit = (methodData) => {
        if (locationRequestInProgress || prayerTimesLoading) return;

        const updatedParams = {
            ...methodData
        };

        if (params.latitude && params.longitude) {
            fetchPrayerTimesByCoordinates({
                ...updatedParams,
                latitude: params.latitude,
                longitude: params.longitude
            });
        } else if (params.address) {
            fetchPrayerTimesByAddress(
                params.address,
                updatedParams
            );
        }
    };

    const handleAllowLocation = () => {
        getCurrentPosition();
        setShowLocationDialog(false);
    };

    const handleDenyLocation = () => {
        updateParams({
            latitude: DEFAULT_LOCATION.latitude,
            longitude: DEFAULT_LOCATION.longitude,
            address: DEFAULT_LOCATION.name
        });

        fetchPrayerTimesByCoordinates({
            latitude: DEFAULT_LOCATION.latitude,
            longitude: DEFAULT_LOCATION.longitude
        });

        setShowLocationDialog(false);
    };

    const handleResetLocationPermission = () => {
        if (navigator.permissions && navigator.permissions.query) {
            navigator.permissions.query({ name: 'geolocation' }).then(result => {
                if (result.state === 'denied') {
                    setOpenPermissionInstructions(true);
                } else {
                    getCurrentPosition();
                }
            });
        } else {
            getCurrentPosition();
        }
    };

    const clockViewContent = useMemo(() => (
        <>
            <PrayerTimeline />
            <AthanPlayer ref={athanAudioRef} />
            <EnhancedPrayerTimesDisplay />
        </>
    ), []);

    const tableViewContent = useMemo(() => (
        <PrayerTimesDisplay
            prayerTimes={prayerTimes || {}}
            loading={isLoading}
        />
    ), [prayerTimes, isLoading]);

    useEffect(() => {
        timerWorkerRef.current = new Worker('/timer.worker.js');

        timerWorkerRef.current.onmessage = (event) => {
            const { type } = event.data;

            if (type === 'prayerUpdate') {
                nextPrayerRef.current = {
                    name: event.data.nextPrayer,
                    minutesRemaining: event.data.minutesRemaining
                };
            } 
            else if (type === 'athanTime') {
                console.log(`It's time for ${event.data.prayer} prayer!`);
                playAthan(event.data.prayer);
                showPrayerNotification(event.data.prayer);
            }
            else if (type === 'showNotification') {
                showPrayerNotification(event.data.prayer, event.data.title, event.data.body);
            }
            else if (type === 'wakeup') {
                console.log('Received wakeup signal for prayer:', event.data.prayer);
                refreshPrayerTimes();
                scheduleWakeupBeforePrayer(event.data);
            }
            else if (type === 'pong') {
                lastWorkerPingRef.current = Date.now();
            }
            else if (type === 'heartbeat') {
            }
        };

        const handleAthanSettingsChange = (event) => {
            if (event.key === 'athanSettings') {
                try {
                    const settings = JSON.parse(event.newValue);
                    athanSettingsRef.current = settings;

                    if (timerWorkerRef.current) {
                        timerWorkerRef.current.postMessage({
                            command: 'updateSettings',
                            athanSettings: settings
                        });
                    }
                } catch (error) {
                    console.error('Error parsing Athan settings:', error);
                }
            }
        };

        try {
            const storedSettings = localStorage.getItem('athanSettings');
            if (storedSettings) {
                athanSettingsRef.current = JSON.parse(storedSettings);
            }
        } catch (error) {
            console.error('Error loading initial Athan settings:', error);
        }

        window.addEventListener('storage', handleAthanSettingsChange);

        timerWorkerRef.current.postMessage({ command: 'ping' });

        return () => {
            if (timerWorkerRef.current) {
                timerWorkerRef.current.postMessage({ command: 'stop' });
                timerWorkerRef.current.terminate();
            }

            if (wakeupTimeoutRef.current) {
                clearTimeout(wakeupTimeoutRef.current);
            }

            window.removeEventListener('storage', handleAthanSettingsChange);
        };
    }, []);

    useEffect(() => {
        localStorage.setItem('keepPrayerTimesActive', keepPageActive);

        if (keepPageActive) {
            startBackgroundProcessing();
            setSnackbarMessage('Prayer times will stay active in the background');
            setSnackbarOpen(true);
        } else {
            stopBackgroundProcessing();
        }
    }, [keepPageActive]);

    useEffect(() => {
        const handleVisibilityChange = () => {
            if (document.visibilityState === 'visible') {
                console.log('Page is now visible');
                if (keepPageActive && timerWorkerRef.current) {
                    checkWorkerStatus();
                    refreshPrayerTimes();
                }
            } else {
                console.log('Page is now hidden');
                if (keepPageActive) {
                }
            }
        };

        document.addEventListener('visibilitychange', handleVisibilityChange);

        return () => {
            document.removeEventListener('visibilitychange', handleVisibilityChange);
        };
    }, [keepPageActive]);

    const startBackgroundProcessing = () => {
        if (timerWorkerRef.current) {
            timerWorkerRef.current.postMessage({
                command: 'start',
                interval: 1000,
                prayerTimes,
                athanSettings: athanSettingsRef.current
            });

            monitorWorkerHealth();
        }

        requestNotificationPermission();
    };

    const stopBackgroundProcessing = () => {
        if (timerWorkerRef.current) {
            timerWorkerRef.current.postMessage({ command: 'stop' });
        }
    };

    const requestNotificationPermission = () => {
        if ('Notification' in window && Notification.permission !== 'granted') {
            Notification.requestPermission().then(permission => {
                if (permission === 'granted') {
                    console.log('Notification permission granted');
                    setSnackbarMessage('Notifications enabled for prayer times');
                    setSnackbarOpen(true);
                } else {
                    console.log('Notification permission denied');
                    setSnackbarMessage('Please enable notifications for prayer time alerts');
                    setSnackbarOpen(true);
                }
            });
        }
    };

    const playAthan = (prayerName) => {
        if (athanAudioRef.current && athanAudioRef.current.playAthan) {
            // Only play Fajar Athan sound for Fajr, otherwise play default/user-selected
            if (prayerName === 'Fajr') {
                athanAudioRef.current.playAthan('Fajr');
            } else {
                athanAudioRef.current.playAthan('default');
            }
            return true;
        }

        try {
            let audio;
            const athanSettings = athanSettingsRef.current;
            if (prayerName === 'Fajr') {
                // Always play Fajr Athan sound for Fajr
                audio = new Audio('/audio/athan/Fajr.mp3');
            } else if (athanSettings && athanSettings.athanSound) {
                audio = new Audio(`/audio/athan/${athanSettings.athanSound}.mp3`);
            } else {
                audio = new Audio('/audio/athan/default.mp3');
            }

            audio.onerror = (error) => {
                console.error('Error playing Athan audio:', error);
                if (audio.src !== '/audio/athan/default.mp3') {
                    audio.src = '/audio/athan/default.mp3';
                    audio.play().catch(e => console.error('Failed to play fallback Athan:', e));
                }
            };

            audio.play()
                .then(() => {
                    console.log(`Playing Athan for ${prayerName} prayer`);
                    return true;
                })
                .catch(error => {
                    console.error('Failed to play Athan:', error);
                    return false;
                });
        } catch (error) {
            console.error('Error setting up Athan audio:', error);
            return false;
        }
    };

    const showPrayerNotification = (prayerName, title, body) => {
        if (!('Notification' in window) || Notification.permission !== 'granted') {
            return;
        }

        const notificationTitle = title || `Time for ${prayerName} Prayer`;
        const notificationBody = body || `It's time to pray ${prayerName}`;

        if (serviceWorkerRegistrationRef.current) {
            serviceWorkerRegistrationRef.current.showNotification(notificationTitle, {
                body: notificationBody,
                icon: '/favicon.svg',
                badge: '/favicon.svg',
                tag: 'prayer-time',
                renotify: true,
                vibrate: [200, 100, 200],
                data: {
                    url: '/',
                    prayer: prayerName
                }
            }).catch(error => {
                console.error('Error showing notification through service worker:', error);
                new Notification(notificationTitle, {
                    body: notificationBody,
                    icon: '/favicon.svg'
                });
            });
        } else {
            new Notification(notificationTitle, {
                body: notificationBody,
                icon: '/favicon.svg'
            });
        }
    };

    const scheduleWakeupBeforePrayer = (prayerData) => {
        if (wakeupTimeoutRef.current) {
            clearTimeout(wakeupTimeoutRef.current);
        }

        const prayerTime = new Date(prayerData.dateTime);
        const now = new Date();
        const timeUntilPrayer = prayerTime - now;

        if (timeUntilPrayer > 0 && timeUntilPrayer < 10 * 60 * 1000) {
            console.log(`Scheduling wakeup for ${prayerData.prayer} in ${Math.floor(timeUntilPrayer/1000)} seconds`);

            const wakeupDelay = Math.max(0, timeUntilPrayer - 10000);

            wakeupTimeoutRef.current = setTimeout(() => {
                refreshPrayerTimes();
                playAthan(prayerData.prayer);
            }, wakeupDelay);
        }
    };

    const monitorWorkerHealth = () => {
        const checkInterval = setInterval(() => {
            if (!keepPageActive) {
                clearInterval(checkInterval);
                return;
            }

            checkWorkerStatus();
        }, 60000);

        return () => clearInterval(checkInterval);
    };

    const checkWorkerStatus = () => {
        if (!timerWorkerRef.current) {
            return;
        }

        timerWorkerRef.current.postMessage({ command: 'ping' });

        setTimeout(() => {
            const now = Date.now();
            if (now - lastWorkerPingRef.current > 2 * 60 * 1000) {
                console.warn('Worker seems unresponsive, restarting...');
                if (timerWorkerRef.current) {
                    timerWorkerRef.current.terminate();
                }

                timerWorkerRef.current = new Worker('/timer.worker.js');
                startBackgroundProcessing();
            }
        }, 5000);
    };

    const refreshPrayerTimes = () => {
        if (!params.latitude || !params.longitude || prayerTimesLoading) {
            return;
        }

        console.log('Refreshing prayer times');
        fetchPrayerTimesByCoordinates({
            latitude: params.latitude,
            longitude: params.longitude
        });

        localStorage.setItem('lastPrayerTimesRefresh', new Date().toISOString());
    };

    return (
        <Container maxWidth="lg" sx={{ py: 4 }}>
            <Helmet>
                <title>Accurate Prayer Times - iMuslim Life</title>
                <meta name="description" content="Get accurate Islamic prayer times (Salat) for any location worldwide. Supports multiple calculation methods, Asr juristic methods, and high latitude adjustments." />
                <meta name="keywords" content="prayer times, salat times, islamic prayer times, muslim prayer, fajr, dhuhr, asr, maghrib, isha, calculation methods" />
                <meta property="og:title" content="Accurate Prayer Times - iMuslim Life" />
                <meta property="og:description" content="Get accurate Islamic prayer times (Salat) for any location worldwide. Supports multiple calculation methods and adjustments." />
                <link rel="canonical" href="https://imuslimlife.com/#/prayer-times" />
            </Helmet>
            <Box sx={{ py: 4 }}>
                <Typography variant="h4" component="h1" gutterBottom sx={{ color: 'primary.main' }}>
                    Prayer Times
                </Typography>

                {prayerTimesError && (
                    <Alert severity="error" sx={{ mb: 3 }}>
                        {prayerTimesError}
                    </Alert>
                )}

                {locationError && !location && (
                    <Alert 
                        severity="info" 
                        sx={{ mb: 3 }}
                        action={
                            <Button 
                                color="inherit" 
                                size="small"
                                onClick={handleResetLocationPermission}
                            >
                                Enable Location
                            </Button>
                        }
                    >
                        Using default location (Orlando, FL). For the most accurate prayer times, please enable location access.
                    </Alert>
                )}

                <Grid container spacing={4}>
                    <Grid item xs={12} md={4}>
                        <LocationForm onSubmit={handleLocationSubmit} />

                        <Box sx={{ mt: 3 }}>
                            <CalculationMethodForm onSubmit={handleMethodSubmit} />
                        </Box>
                    </Grid>

                    <Grid item xs={12} md={8}>
                        <Tabs
                            value={displayMode}
                            onChange={(e, newValue) => setDisplayMode(newValue)}
                            sx={{ mb: 3 }}
                        >
                            <Tab value="clocks" label="Clock View" />
                            <Tab value="table" label="Table View" />
                        </Tabs>

                        {isLoading && !prayerTimes ? (
                            <Box sx={{ display: 'flex', justifyContent: 'center', my: 4 }}>
                                <CircularProgress />
                                <Typography variant="body1" sx={{ ml: 2 }}>
                                    {locationLoading ? 'Getting your location...' : 'Loading prayer times...'}
                                </Typography>
                            </Box>
                        ) : (
                            displayMode === 'clocks' ? clockViewContent : tableViewContent
                        )}
                    </Grid>
                </Grid>
            </Box>

            <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                <FormControlLabel
                    control={
                        <Switch
                            checked={keepPageActive}
                            onChange={(e) => setKeepPageActive(e.target.checked)}
                            color="primary"
                        />
                    }
                    label="Keep prayer times active in background"
                />
            </Box>

            <AthanSettings />

            <Dialog
                open={showLocationDialog}
                onClose={handleDenyLocation}
                disableEscapeKeyDown
            >
                <DialogTitle>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <LocationOnIcon sx={{ mr: 1, color: 'primary.main' }} />
                        Allow Location Access
                    </Box>
                </DialogTitle>
                <DialogContent>
                    <Typography paragraph>
                        Salat-e-Mustaqeem needs your location to provide accurate prayer times for where you are.
                    </Typography>
                    <Typography paragraph>
                        When prompted by your browser, please click "Allow" to share your location.
                    </Typography>
                    <Typography paragraph sx={{ fontStyle: 'italic', color: 'text.secondary' }}>
                        If you don't allow location access, we'll use Orlando, Florida as the default location.
                        You can always manually enter your location.
                    </Typography>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDenyLocation}>
                        Use Default Location
                    </Button>
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={handleAllowLocation}
                        startIcon={<LocationOnIcon />}
                    >
                        Allow Location Access
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={openPermissionInstructions}
                onClose={() => setOpenPermissionInstructions(false)}
            >
                <DialogTitle>Enable Location Access</DialogTitle>
                <DialogContent>
                    <Typography paragraph>
                        You previously denied location access. To enable it:
                    </Typography>
                    
                    <Typography component="div" paragraph>
                        <ol>
                            <li>Click on the lock/info icon in your browser's address bar</li>
                            <li>Find "Location" or "Site settings"</li>
                            <li>Change from "Block" to "Allow"</li>
                            <li>Refresh this page</li>
                        </ol>
                    </Typography>
                    
                    <Typography paragraph sx={{ fontStyle: 'italic' }}>
                        Instructions may vary depending on your browser.
                    </Typography>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setOpenPermissionInstructions(false)}>
                        OK, Got It
                    </Button>
                </DialogActions>
            </Dialog>
            
            <Snackbar
                open={snackbarOpen}
                autoHideDuration={6000}
                onClose={() => setSnackbarOpen(false)}
                message={snackbarMessage}
            />
        </Container>
    );
};

export default PrayerTimesPage;