import React, { useState, useEffect } from 'react';
import {
    Card,
    CardHeader,
    CardContent,
    FormControl,
    FormLabel,
    RadioGroup,
    Radio,
    FormControlLabel,
    TextField,
    Button,
    InputAdornment,
    Box,
    Typography,
    Alert,
    CircularProgress,
    Collapse
} from '@mui/material';
import {
    ExpandMore as ExpandMoreIcon,
    LocationOn as LocationOnIcon,
    MyLocation as MyLocationIcon,
    Search as SearchIcon,
    CalendarMonth as CalendarMonthIcon
} from '@mui/icons-material';
import { format } from 'date-fns';
import { usePrayerTimes } from '../../contexts/PrayerTimesContext';
import useGeolocation from '../../hooks/useGeolocation';
import { DEFAULT_LOCATION } from '../../utils/constants';

const LocationForm = ({ onSubmit }) => {
    // Location type: coordinates or address
    const [locationType, setLocationType] = useState('coordinates');

    // Form fields
    const [latitude, setLatitude] = useState(DEFAULT_LOCATION.latitude.toString());
    const [longitude, setLongitude] = useState(DEFAULT_LOCATION.longitude.toString());
    const [address, setAddress] = useState(DEFAULT_LOCATION.address);

    // Use a simple date string instead of date picker
    const [selectedDate, setSelectedDate] = useState(new Date());
    const [dateString, setDateString] = useState(format(new Date(), 'yyyy-MM-dd'));

    // UI state
    const [error, setError] = useState(null);
    const [expanded, setExpanded] = useState(true);

    // Get geolocation hook
    const { location, loading: geoLoading, error: geoError, getCurrentPosition } = useGeolocation();

    // Prayer times context
    const { params, updateParams } = usePrayerTimes();

    // Update form when location is obtained via geolocation
    useEffect(() => {
        if (location) {
            setLatitude(location.latitude.toFixed(6));
            setLongitude(location.longitude.toFixed(6));
            setLocationType('coordinates');

            // Update params in context
            updateParams({
                latitude: location.latitude,
                longitude: location.longitude
            });
        }
    }, [location, updateParams]);

    // Handle location type change
    const handleLocationTypeChange = (event) => {
        setLocationType(event.target.value);
    };

    // Handle date change
    const handleDateChange = (event) => {
        const newDateStr = event.target.value;
        setDateString(newDateStr);

        // Convert to Date object if valid
        const dateObj = new Date(newDateStr);
        if (!isNaN(dateObj.getTime())) {
            setSelectedDate(dateObj);
        }
    };

    // Handle form submission
    const handleSubmit = (event) => {
        event.preventDefault();
        setError(null);

        try {
            // Validate inputs
            if (locationType === 'coordinates') {
                const lat = parseFloat(latitude);
                const lng = parseFloat(longitude);

                if (isNaN(lat) || isNaN(lng) || lat < -90 || lat > 90 || lng < -180 || lng > 180) {
                    throw new Error('Please enter valid coordinates. Latitude must be between -90 and 90, longitude between -180 and 180.');
                }

                // Update params
                updateParams({
                    latitude: lat,
                    longitude: lng
                });

                // Call onSubmit if provided
                if (onSubmit) {
                    onSubmit({
                        type: 'coordinates',
                        data: { latitude: lat, longitude: lng },
                        date: selectedDate
                    });
                }
            } else {
                // Address validation
                if (!address || address.trim() === '') {
                    throw new Error('Please enter a valid address.');
                }

                // Call onSubmit if provided
                if (onSubmit) {
                    onSubmit({
                        type: 'address',
                        data: { address },
                        date: selectedDate
                    });
                }
            }
        } catch (err) {
            setError(err.message);
        }
    };

    // Handle geolocation button click
    const handleUseMyLocation = () => {
        getCurrentPosition();
    };

    // Toggle form expansion
    const toggleExpanded = () => {
        setExpanded(!expanded);
    };

    return (
        <Card elevation={2}>
            <CardHeader
                title={
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <LocationOnIcon sx={{ mr: 1, color: 'primary.main' }} />
                        <Typography variant="h6" component="span">Location</Typography>
                        <Button
                            size="small"
                            onClick={toggleExpanded}
                            sx={{ ml: 'auto' }}
                            endIcon={<ExpandMoreIcon sx={{
                                transform: expanded ? 'rotate(180deg)' : 'rotate(0deg)',
                                transition: 'transform 0.3s'
                            }} />}
                        >
                            {expanded ? 'Hide' : 'Show'}
                        </Button>
                    </Box>
                }
                sx={{
                    bgcolor: 'background.default',
                    borderBottom: '1px solid',
                    borderColor: 'divider'
                }}
            />
            <Collapse in={expanded}>
                <CardContent>
                    <form onSubmit={handleSubmit}>
                        <FormControl component="fieldset" sx={{ mb: 2, width: '100%' }}>
                            <FormLabel component="legend">Location Method</FormLabel>
                            <RadioGroup
                                row
                                name="location-type"
                                value={locationType}
                                onChange={handleLocationTypeChange}
                            >
                                <FormControlLabel
                                    value="coordinates"
                                    control={<Radio />}
                                    label="Coordinates"
                                />
                                <FormControlLabel
                                    value="address"
                                    control={<Radio />}
                                    label="Address / City"
                                />
                            </RadioGroup>
                        </FormControl>

                        {geoError && (
                            <Alert severity="warning" sx={{ mb: 2 }}>
                                {geoError.message || 'Unable to get your location. Please enter it manually.'}
                            </Alert>
                        )}

                        {error && (
                            <Alert severity="error" sx={{ mb: 2 }}>
                                {error}
                            </Alert>
                        )}

                        <Box sx={{ display: locationType === 'coordinates' ? 'block' : 'none' }}>
                            <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
                                <TextField
                                    label="Latitude"
                                    value={latitude}
                                    onChange={(e) => setLatitude(e.target.value)}
                                    type="number"
                                    inputProps={{ step: 0.000001 }}
                                    fullWidth
                                    required={locationType === 'coordinates'}
                                    helperText="Between -90 and 90"
                                    InputLabelProps={{ shrink: true }}
                                    placeholder="e.g., 28.6694"
                                />
                                <TextField
                                    label="Longitude"
                                    value={longitude}
                                    onChange={(e) => setLongitude(e.target.value)}
                                    type="number"
                                    inputProps={{ step: 0.000001 }}
                                    fullWidth
                                    required={locationType === 'coordinates'}
                                    helperText="Between -180 and 180"
                                    InputLabelProps={{ shrink: true }}
                                    placeholder="e.g., -81.4279"
                                />
                            </Box>
                            <Button
                                startIcon={geoLoading ? <CircularProgress size={20} /> : <MyLocationIcon />}
                                onClick={handleUseMyLocation}
                                disabled={geoLoading}
                                variant="outlined"
                                size="small"
                                fullWidth
                                sx={{ mb: 2 }}
                            >
                                {geoLoading ? 'Getting Location...' : 'Use My Current Location'}
                            </Button>
                        </Box>

                        <Box sx={{ display: locationType === 'address' ? 'block' : 'none', mb: 2 }}>
                            <TextField
                                label="Address or City"
                                value={address}
                                onChange={(e) => setAddress(e.target.value)}
                                required={locationType === 'address'}
                                fullWidth
                                placeholder="e.g., Orlando, FL"
                                InputLabelProps={{ shrink: true }}
                                InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <SearchIcon />
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        </Box>

                        <TextField
                            label="Date"
                            type="date"
                            value={dateString}
                            onChange={handleDateChange}
                            fullWidth
                            margin="normal"
                            InputLabelProps={{
                                shrink: true,
                            }}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <CalendarMonthIcon />
                                    </InputAdornment>
                                ),
                            }}
                        />

                        <Button
                            type="submit"
                            variant="contained"
                            color="primary"
                            fullWidth
                            size="large"
                            sx={{ mt: 2 }}
                            startIcon={<LocationOnIcon />}
                        >
                            Set Location
                        </Button>
                    </form>
                </CardContent>
            </Collapse>
        </Card>
    );
};

export default LocationForm;