import React, { memo } from 'react';
import {
    Box,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    Typography,
    Skeleton,
    Card,
    CardContent,
    CircularProgress
} from '@mui/material';
import { usePrayerTimes } from '../../contexts/PrayerTimesContext';
import {
    CALCULATION_METHODS,
    HIGH_LATITUDE_METHODS,
    JURISTIC_METHODS,
    MIDNIGHT_MODES
} from '../../utils/constants';

// Helper functions defined outside component to prevent recreation on each render
const formatLatitudeMethod = (methodValue) => {
    // If it's a number, convert to string representation
    if (typeof methodValue === 'number') {
        const methods = {
            0: 'NONE',
            1: 'NIGHT_MIDDLE',
            2: 'ONE_SEVENTH',
            3: 'ANGLE_BASED'
        };
        return methods[methodValue] ? methods[methodValue].replace('_', ' ') : 'ANGLE BASED';
    }

    // Now that we have a string, format it for display
    return methodValue ? methodValue.replace('_', ' ') : 'ANGLE BASED';
};

const formatMidnightMode = (modeValue) => {
    // If it's a number, convert to string representation
    if (typeof modeValue === 'number') {
        const modes = {
            0: 'STANDARD',
            1: 'JAFARI'
        };
        return modes[modeValue] || 'STANDARD';
    }

    return modeValue || 'STANDARD';
};

const getMethodName = (methodId) => {
    const method = CALCULATION_METHODS.find(m => m.id === methodId);
    return method ? method.name : 'Unknown';
};

const getJuristicName = (juristicId) => {
    const method = JURISTIC_METHODS.find(m => m.id === juristicId);
    return method ? method.name : 'Standard';
};

const formatIslamicDate = (hijriDate) => {
    if (!hijriDate) return '';
    return `${hijriDate.day} ${hijriDate.month.en} ${hijriDate.year} AH`;
};

// Use memo to prevent unnecessary re-renders
const PrayerTimesDisplay = memo(({ prayerTimes, loading }) => {
    const { params, hijriDate } = usePrayerTimes();

    // If prayer times are loading or unavailable, show loading indicator
    if (loading || !prayerTimes || Object.keys(prayerTimes).length === 0) {
        return (
            <Box sx={{ display: 'flex', justifyContent: 'center', my: 4 }}>
                <CircularProgress />
                <Typography variant="body1" sx={{ ml: 2 }}>
                    Loading prayer times...
                </Typography>
            </Box>
        );
    }

    // Filter unwanted prayers once
    const filteredPrayers = Object.entries(prayerTimes)
        .filter(([key]) => !['Sunset', 'Imsak', 'Firstthird', 'Lastthird'].includes(key));

    // Pre-calculate values to avoid doing it in JSX
    const locationText = params.address || `${params.latitude}, ${params.longitude}`;
    const dateText = hijriDate ? formatIslamicDate(hijriDate) : '';
    const calculationMethod = getMethodName(params.method);
    const juristicMethod = getJuristicName(params.school);

    // Try-catch to safely handle any potential errors in formatting
    let latitudeMethodText, midnightModeText;
    try {
        latitudeMethodText = formatLatitudeMethod(params.latitudeAdjustmentMethod);
        midnightModeText = formatMidnightMode(params.midnightMode);
    } catch (error) {
        console.error("Error formatting params:", error);
        latitudeMethodText = "Angle Based";
        midnightModeText = "Standard";
    }

    return (
        <Box>
            {/* Prayer times table */}
            <Card sx={{ mb: 4 }}>
                <CardContent>
                    <Typography variant="h6" gutterBottom>Prayer Times</Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                        {locationText}
                        {dateText && ` - ${dateText}`}
                    </Typography>

                    <TableContainer component={Paper} variant="outlined">
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell><strong>Prayer</strong></TableCell>
                                    <TableCell align="right"><strong>Time</strong></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {filteredPrayers.map(([prayer, time]) => (
                                    <TableRow key={prayer}>
                                        <TableCell component="th" scope="row">
                                            {prayer}
                                        </TableCell>
                                        <TableCell align="right">{time}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </CardContent>
            </Card>

            {/* Calculation settings info */}
            <Card>
                <CardContent>
                    <Typography variant="h6" gutterBottom>Calculation Settings</Typography>
                    <TableContainer component={Paper} variant="outlined">
                        <Table size="small">
                            <TableBody>
                                <TableRow>
                                    <TableCell component="th" scope="row">Calculation Method</TableCell>
                                    <TableCell align="right">{calculationMethod}</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">Juristic Method (Asr)</TableCell>
                                    <TableCell align="right">{juristicMethod}</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">High Latitude Adjustment</TableCell>
                                    <TableCell align="right">{latitudeMethodText}</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">Midnight Mode</TableCell>
                                    <TableCell align="right">{midnightModeText}</TableCell>
                                </TableRow>
                                {params.timezone && (
                                    <TableRow>
                                        <TableCell component="th" scope="row">Timezone</TableCell>
                                        <TableCell align="right">{params.timezone}</TableCell>
                                    </TableRow>
                                )}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </CardContent>
            </Card>
        </Box>
    );
});

PrayerTimesDisplay.displayName = 'PrayerTimesDisplay';

export default PrayerTimesDisplay;