import React, { useState, useEffect } from 'react';
import { Box, Card, CardContent, Typography, useTheme, Paper } from '@mui/material';
import { usePrayerTimes } from '../../contexts/PrayerTimesContext';
import { formatTime12Hour } from '../../utils/formatters';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import NightlightIcon from '@mui/icons-material/Nightlight';
import WbTwilightIcon from '@mui/icons-material/WbTwilight';
import LightModeIcon from '@mui/icons-material/LightMode';
import { motion } from 'framer-motion';

/**
 * A visual timeline showing prayer times and other significant times throughout the day
 */
const PrayerTimeline = () => {
    const theme = useTheme();
    const { prayerTimes } = usePrayerTimes();
    const [currentTime, setCurrentTime] = useState(new Date());
    const [timelineData, setTimelineData] = useState([]);
    const [activePrayerIndex, setActivePrayerIndex] = useState(-1);
    const [dayProgress, setDayProgress] = useState(0);

    // Update current time every minute
    useEffect(() => {
        const timer = setInterval(() => {
            setCurrentTime(new Date());
        }, 60000);
        return () => clearInterval(timer);
    }, []);

    // Process prayer times into timeline data
    useEffect(() => {
        if (!prayerTimes) return;

        const timeOrder = [
            { key: 'Imsak', name: 'Imsak', isPrayer: false, icon: <WbTwilightIcon fontSize="small" /> },
            { key: 'Fajr', name: 'Fajr', isPrayer: true, icon: <WbTwilightIcon fontSize="small" /> },
            { key: 'Sunrise', name: 'Sunrise', isPrayer: false, icon: <LightModeIcon fontSize="small" /> },
            { key: 'Dhuhr', name: 'Dhuhr', isPrayer: true, icon: <AccessTimeIcon fontSize="small" /> },
            { key: 'Asr', name: 'Asr', isPrayer: true, icon: <AccessTimeIcon fontSize="small" /> },
            { key: 'Maghrib', name: 'Maghrib', isPrayer: true, icon: <WbTwilightIcon fontSize="small" /> },
            { key: 'Isha', name: 'Isha', isPrayer: true, icon: <NightlightIcon fontSize="small" /> },
            { key: 'Midnight', name: 'Midnight', isPrayer: false, icon: <NightlightIcon fontSize="small" /> },
            { key: 'Firstthird', name: '1st Third', isPrayer: false, icon: <NightlightIcon fontSize="small" /> },
            { key: 'Lastthird', name: 'Last Third', isPrayer: false, icon: <NightlightIcon fontSize="small" /> }
        ];

        const timeline = [];

        // Process each time point
        timeOrder.forEach(item => {
            if (prayerTimes[item.key]) {
                const [hours, minutes] = prayerTimes[item.key].split(':').map(Number);
                let date = new Date();

                // For midnight and night portions which might be after midnight
                if (item.key === 'Midnight' || item.key === 'Firstthird' || item.key === 'Lastthird') {
                    const hour24 = parseInt(hours);
                    // If the time is early morning (likely for these night times)
                    if (hour24 < 6) {
                        date.setDate(date.getDate() + 1); // Set to tomorrow
                    }
                }

                date.setHours(hours, minutes, 0, 0);

                timeline.push({
                    name: item.name,
                    key: item.key,
                    time: date,
                    formattedTime: formatTime12Hour(prayerTimes[item.key]),
                    isPrayer: item.isPrayer,
                    icon: item.icon
                });
            }
        });

        // Sort by time
        const sortedTimeline = timeline.sort((a, b) => a.time - b.time);
        setTimelineData(sortedTimeline);

    }, [prayerTimes]);

    // Calculate day progress and active prayer - separate effect
    useEffect(() => {
        if (!timelineData.length) return;

        const now = currentTime;

        // Calculate day progress percentage
        const dayStart = new Date(now);
        dayStart.setHours(0, 0, 0, 0);
        const dayEnd = new Date(now);
        dayEnd.setHours(23, 59, 59, 999);

        const totalMillis = dayEnd - dayStart;
        const elapsedMillis = now - dayStart;
        const progress = Math.min(elapsedMillis / totalMillis, 1) * 100;
        setDayProgress(progress);

        // Find active time point (last time point before current time)
        let activeIndex = -1;
        for (let i = timelineData.length - 1; i >= 0; i--) {
            if (timelineData[i].time <= now) {
                activeIndex = i;
                break;
            }
        }
        setActivePrayerIndex(activeIndex);
    }, [timelineData, currentTime]);

    // Get a color for each time point based on its type
    const getTimePointColor = (timePoint, isPast, isActive) => {
        if (isActive) return theme.palette.secondary.main;
        if (isPast) return theme.palette.success.main;

        // Different colors for different types of time points
        if (timePoint.key === 'Sunrise') return theme.palette.warning.main;
        if (timePoint.isPrayer) return theme.palette.primary.main;
        if (timePoint.key === 'Imsak') return theme.palette.info.main;
        if (['Midnight', 'Firstthird', 'Lastthird'].includes(timePoint.key)) {
            return theme.palette.text.secondary;
        }

        return theme.palette.text.secondary;
    };

    if (!prayerTimes || timelineData.length === 0) {
        return null;
    }

    return (
        <Card sx={{ mb: 4 }}>
            <CardContent>
                <Typography variant="h6" gutterBottom>
                    Daily Prayer Timeline
                </Typography>

                {/* Timeline container */}
                <Paper
                    elevation={0}
                    sx={{
                        position: 'relative',
                        height: 180, // Original height
                        p: 3,
                        backgroundColor: theme.palette.background.default
                    }}
                >
                    {/* Main timeline line with original padding */}
                    <Box
                        sx={{
                            position: 'relative',
                            height: 4,
                            backgroundColor: theme.palette.divider,
                            mt: 6, // Original top margin/padding
                            borderRadius: 2,
                            overflow: 'visible'
                        }}
                    >
                        {/* Day progress indicator (animated) */}
                        <motion.div
                            initial={{ width: 0 }}
                            animate={{ width: `${dayProgress}%` }}
                            transition={{ type: 'spring', stiffness: 80, damping: 20 }}
                            style={{
                                position: 'absolute',
                                height: '100%',
                                backgroundColor: theme.palette.primary.main,
                                borderRadius: 8,
                                opacity: 0.7
                            }}
                        />

                        {/* Current time marker (animated with pulse) */}
                        <motion.div
                            initial={false}
                            animate={{ left: `${dayProgress}%` }}
                            transition={{ type: 'spring', stiffness: 80, damping: 20 }}
                            style={{
                                position: 'absolute',
                                height: 16,
                                width: 16,
                                backgroundColor: theme.palette.secondary.main,
                                borderRadius: '50%',
                                top: '50%',
                                transform: 'translate(-50%, -50%)',
                                boxShadow: `0 0 0 4px ${theme.palette.background.paper}`,
                                zIndex: 10
                            }}
                        >
                            {/* Pulse effect */}
                            <motion.div
                                initial={{ scale: 1, opacity: 0.7 }}
                                animate={{ scale: [1, 1.5, 1], opacity: [0.7, 0, 0.7] }}
                                transition={{
                                    duration: 1.5,
                                    repeat: Infinity,
                                    ease: 'easeInOut',
                                }}
                                style={{
                                    position: 'absolute',
                                    top: '50%',
                                    left: '50%',
                                    width: 32,
                                    height: 32,
                                    borderRadius: '50%',
                                    backgroundColor: theme.palette.secondary.main,
                                    opacity: 0.5,
                                    transform: 'translate(-50%, -50%)',
                                    zIndex: 1,
                                }}
                            />
                        </motion.div>

                        {/* Time points with icons and better visualization but original spacing */}
                        {timelineData.map((timePoint, index) => {
                            // Calculate position as percentage of day
                            const timeOfDay = timePoint.time.getHours() * 3600 + timePoint.time.getMinutes() * 60;
                            const percentage = (timeOfDay / 86400) * 100;

                            const isActive = index === activePrayerIndex;
                            const isPast = timePoint.time < currentTime;
                            const timePointColor = getTimePointColor(timePoint, isPast, isActive);

                            // Use original label positioning logic as the baseline
                            const isLabelOnTop = index % 2 === 0;

                            // Special case for Imsak and Sunrise to prevent overlap
                            // If it's Imsak, always show on bottom; if it's Sunrise, always show on top
                            let finalPosition;
                            if (timePoint.key === 'Imsak') {
                                finalPosition = 15; // Always bottom for Imsak
                            } else if (timePoint.key === 'Sunrise') {
                                finalPosition = -35; // Always top for Sunrise
                            } else {
                                finalPosition = isLabelOnTop ? -35 : 15; // Regular alternating pattern for others
                            }

                            return (
                                <React.Fragment key={timePoint.key}>
                                    {/* Glowing prayer dot (animated) */}
                                    <motion.div
                                        initial={{ scale: 0, opacity: 0 }}
                                        animate={{
                                            left: `${percentage}%`,
                                            scale: isActive ? 1.3 : 1,
                                            opacity: 1,
                                            boxShadow: isActive || timePoint.isPrayer ? `0 0 12px 4px ${timePointColor}` : 'none',
                                        }}
                                        transition={{ type: 'spring', stiffness: 120, damping: 15 }}
                                        style={{
                                            position: 'absolute',
                                            height: timePoint.isPrayer ? 14 : 10,
                                            width: timePoint.isPrayer ? 14 : 10,
                                            backgroundColor: timePointColor,
                                            border: isActive ? `3px solid ${theme.palette.secondary.main}` : 'none',
                                            borderRadius: '50%',
                                            top: '50%',
                                            transform: 'translate(-50%, -50%)',
                                            zIndex: isActive ? 5 : 1,
                                        }}
                                    >
                                        {/* Continuous glow animation for prayer dots */}
                                        {timePoint.isPrayer && (
                                            <motion.div
                                                initial={{ scale: 1, opacity: 0.7 }}
                                                animate={{ scale: [1, 1.7, 1], opacity: [0.7, 0, 0.7] }}
                                                transition={{ duration: 2, repeat: Infinity, ease: 'easeInOut' }}
                                                style={{
                                                    position: 'absolute',
                                                    top: '50%',
                                                    left: '50%',
                                                    width: 32,
                                                    height: 32,
                                                    borderRadius: '50%',
                                                    backgroundColor: timePointColor,
                                                    opacity: 0.4,
                                                    transform: 'translate(-50%, -50%)',
                                                    zIndex: 0,
                                                }}
                                            />
                                        )}
                                    </motion.div>

                                    {/* Time point label (fade/slide in) with extra padding from progress bar */}
                                    <motion.div
                                        initial={{ opacity: 0, y: isLabelOnTop ? -10 : 10 }}
                                        animate={{
                                            left: `${percentage}%`,
                                            opacity: 1,
                                            y: 0,
                                            scale: isActive ? 1.05 : 1
                                        }}
                                        transition={{ type: 'spring', stiffness: 100, damping: 18 }}
                                        style={{
                                            position: 'absolute',
                                            top: finalPosition + (isLabelOnTop ? -16 : 16), // Add padding above/below progress bar
                                            transform: 'translateX(-50%)',
                                            textAlign: 'center',
                                            minWidth: 70,
                                            backgroundColor: (isLabelOnTop || timePoint.key === 'Sunrise') ?
                                                theme.palette.background.default :
                                                'transparent',
                                            borderRadius: 4,
                                            padding: '2px 4px',
                                            border: isActive ?
                                                `1px solid ${theme.palette.secondary.main}` :
                                                (isLabelOnTop || timePoint.key === 'Sunrise') ? `1px solid ${theme.palette.divider}` : 'none',
                                            zIndex: isActive ? 5 : (timePoint.key === 'Imsak' || timePoint.key === 'Sunrise') ? 3 : 2
                                        }}
                                    >
                                        <Box sx={{
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                            color: isActive ? timePointColor : 'inherit',
                                            mb: 0.5
                                        }}>
                                            {timePoint.icon &&
                                                <Box component="span" sx={{ mr: 0.5, color: timePointColor, display: 'flex' }}>
                                                    {timePoint.icon}
                                                </Box>
                                            }
                                            <Typography
                                                variant="caption"
                                                component="span"
                                                sx={{
                                                    fontWeight: isActive ? 'bold' : 'normal',
                                                    whiteSpace: 'nowrap',
                                                    fontSize: isActive ? '0.85rem' : '0.75rem'
                                                }}
                                            >
                                                {timePoint.name}
                                            </Typography>
                                        </Box>

                                        <Typography
                                            variant="caption"
                                            sx={{
                                                color: isActive ? timePointColor : theme.palette.text.secondary,
                                                fontSize: isActive ? '0.8rem' : '0.75rem',
                                                display: 'block',
                                                lineHeight: 1.2
                                            }}
                                        >
                                            {timePoint.formattedTime}
                                        </Typography>
                                    </motion.div>
                                </React.Fragment>
                            );
                        })}
                    </Box>

                    {/* Current time display - improved styling but original positioning (animated) */}
                    <motion.div
                        initial={false}
                        animate={{ left: `${dayProgress}%` }}
                        transition={{ type: 'spring', stiffness: 80, damping: 20 }}
                        style={{
                            position: 'absolute',
                            bottom: 10,
                            transform: 'translateX(-50%)',
                            textAlign: 'center',
                            minWidth: 70,
                            zIndex: 5
                        }}
                    >
                        <Typography
                            variant="caption"
                            sx={{
                                backgroundColor: theme.palette.background.paper,
                                px: 1.5,
                                py: 0.5,
                                borderRadius: 1,
                                color: theme.palette.secondary.main,
                                display: 'inline-block',
                                boxShadow: 1,
                                fontWeight: 'medium'
                            }}
                        >
                            {currentTime.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                        </Typography>
                    </motion.div>
                </Paper>

                {/* Enhanced legend with all time point types */}
                <Box sx={{ display: 'flex', justifyContent: 'center', gap: 2, mt: 2, flexWrap: 'wrap' }}>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <Box
                            sx={{
                                width: 10,
                                height: 10,
                                borderRadius: '50%',
                                backgroundColor: theme.palette.primary.main,
                                mr: 1
                            }}
                        />
                        <Typography variant="caption">Prayers</Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <Box
                            sx={{
                                width: 10,
                                height: 10,
                                borderRadius: '50%',
                                backgroundColor: theme.palette.info.main,
                                mr: 1
                            }}
                        />
                        <Typography variant="caption">Imsak</Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <Box
                            sx={{
                                width: 10,
                                height: 10,
                                borderRadius: '50%',
                                backgroundColor: theme.palette.warning.main,
                                mr: 1
                            }}
                        />
                        <Typography variant="caption">Sunrise</Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <Box
                            sx={{
                                width: 10,
                                height: 10,
                                borderRadius: '50%',
                                backgroundColor: theme.palette.text.secondary,
                                mr: 1
                            }}
                        />
                        <Typography variant="caption">Night times</Typography>
                    </Box>
                </Box>
            </CardContent>
        </Card>
    );
};

export default PrayerTimeline;